.actors {
  grid-area: actors;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.actors-table {
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: auto;
  gap: 20px 20px;
}

.actors-table__button {
  margin-top: 20px;
  padding: 12px 20px;
  border: none;
  background-color: #FFFFFF;
  border: 1px solid #E2E5EA;
  border-radius: 16px;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #303030;
  align-self: center;
  cursor: pointer;
}


@media screen and (max-width: 1110px) {
  .actors-table {
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 768px) {
  .actors-table {
    gap: 16px;
  }
}
