.button-back {
  padding: 7px 16px;
  background: #FFFFFF;
  border: 1px solid #E2E5EA;
  border-radius: 16px;
  display: flex;
  gap: 8px;
  align-items: center;
  justify-self: flex-start;
  cursor: pointer;
}

.button-back__span {
  width: 16px;
  height: 16px;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(../../images/balance-button-back.svg);
}

.button-back__text {
  margin: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #303030;
}
