.casting-details {
  grid-area: secondary-information;
}

.casting-details__title {
  margin-top: 0;
  margin-bottom: 16px;
  font-weight: 600;
  font-size: 18px;
  line-height: 26px;
  color: #303030;
}

.casting-details__table {
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  justify-content: space-between;
  gap: 16px 16px;
}

.casting-details__table-cell {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.casting-details__table-cell-title {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
}

.casting-details__table-cell-subtitle {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #242A33;
  white-space: nowrap;
}

@media screen and (max-width: 992px) {
  .casting-details__title {
    font-size: 16px;
    line-height: 24px;
  }

  .casting-details__table {
    grid-template-columns: repeat(3, 1fr);
    gap: 16px 0;
  }
  .casting-details__table-cell_date {
    grid-column-start: 2;
    grid-column-end: 4;
  }
}