.questionary__title {
  margin-top: 0;
  margin-bottom: 4px;
  font-weight: 600;
  font-size: 18px;
  line-height: 26px;
  color: #303030;
}

.questionary__subtitle {
  margin-top: 0;
  margin-bottom: 8px;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
}

.questionary__steps {
  margin-bottom: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.questionary__step {
  height: 4px;
  width: 100%;
  background-color: #F1F3F6;
  border-radius: 2px;
}

.questionary__step_passed {
  background-color: #1BEE6F;
}


@media screen and (max-width: 992px) {
  .questionary__title {
    font-size: 16px;
    line-height: 24px;
  }

  .questionary__subtitle {
    font-size: 10px;
    line-height: 12px;
  }

  .question-list__button {
    width: 100%;
  }
}