.content_castings {
  display: grid;
  grid-template-columns: 1fr auto;
  grid-template-rows: auto 1fr;
  grid-template-areas: 
  'heading heading'
  'castings advertising';
  gap: 0 20px;
}

.castings__container {
  display: flex;
  justify-content: space-between;
  grid-area: heading;
}

.castings__heading {
  display: flex;
  flex-direction: column;
}

.castings__title {
  margin-top: 0;
  margin-bottom: 4px;
  font-weight: 600;
  font-size: 32px;
  line-height: 38px;
  color: #242A33;
}

.castings__subtitle {
  margin-top: 0;
  margin-bottom: 24px;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
}

@media screen and (max-width: 1110px) {
  .content_castings {
    grid-template-columns: 1fr 20%;
  }
}

@media screen and (max-width: 992px) {
  .content_castings {
    grid-template-columns: auto;
    grid-template-areas: 
    'heading'
    'castings';
  }
}

@media screen and (max-width: 768px) {
  .castings__title {
    font-size: 24px;
    line-height: 30px;
  }

  .castings__subtitle {
    margin: 0;
    font-size: 10px;
    line-height: 12px;
  }

  .castings__container {
    margin-bottom: 16px;
    flex-direction: column;
    align-items: stretch;
    gap: 14px;
    position: relative;
  }

  .castings__heading {
    grid-template-areas: heading;
  }
}