.input {
  margin: 0;
  padding: 12px 16px;
  border: 1px solid #E2E5EA;
  border-radius: 16px;
  background-color: #FFFFFF;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #303030;
}

.input_invalid {
  border: 1px solid #FF1F1F !important;
}

.input::placeholder {
  color: #999999;
}

.input__title {
  display: flex;
  flex-direction: column;
  gap: 8px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #303030;
  width: 100%;
  position: relative;
}

.input__title_limited_width {
  max-width: 400px
}

.input__error {
  position: absolute;
  color: #FF1F1F;
  bottom: -20px;
  font-size: 12px;
  font-weight: 400;
}

.input__error_calendar {
  bottom: -40px;
}

@media screen and (max-width: 992px) {
  .input__error_calendar {
    bottom: -20px;
  }
}