.tabs__form {
  margin-top: 32px;
  padding: 24px;
  background: #FFFFFF;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
}

.withdraw__info-container {
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-template-areas: 
  'title ratio'
  'subtitle subtitle';
  gap: 8px 0;
}

.withdraw__title {
  margin: 0;
  font-weight: 600;
  font-size: 18px;
  line-height: 26px;
  color: #303030;
  grid-area: title;
}

.withdraw__subtitle {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
  grid-area: subtitle;
}

.withdraw__ratio {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #636870;
  grid-area: ratio;
  justify-self: flex-end;
  display: flex;
  gap: 4px;
}

.withdraw__ratio-icon {
  width: 16px;
  height: 16px;
  background-image: url(../../../images/info-icon.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.withdraw__button {
  margin-top: 24px;
  border: none;
  padding: 14px 85.5px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #FFFFFF;
  background: #6C26FF;
  box-shadow: 0px 15px 30px rgba(108, 38, 255, 0.15);
  border-radius: 16px;
  cursor: pointer;
}

.withdraw__sum-container {
  margin-top: 24px;
  padding-bottom: 24px;
  display: grid;
  grid-template-columns: minmax(200px, 400px) 1fr;
  grid-template-areas: 
  'sum radio'
  'commission commission';
  gap: 0 24px;
  border-bottom: 1px solid #F1F3F6;
}

.withdraw__commission {
  margin: 0;
  position: absolute;
  bottom: -20px;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #999999;
  grid-area: commission;
}

.withdraw__commission_invisible {
  display: none;
}

.withdraw__data-container {
  margin-top: 24px;
  display: grid;
  gap: 24px;
}

.withdraw__data-container_type_card {
  grid-template-columns: repeat(2, 400px);
}

.withdraw__data-container_type_account {
  grid-template-columns: repeat(3, 1fr);
}

@media screen and (max-width: 992px) {
  .withdraw__info-container {
    display: flex;
    flex-direction: column;
    gap: 0;
  }

  .withdraw__subtitle {
    margin-top: 8px;
    margin-bottom: 16px;
  }

  .withdraw__sum-container {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  .withdraw__commission {
    margin-top: -8px;
  }

  .withdraw__data-container {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  .withdraw__button {
    width: 100%;
  }
}

@media screen and (max-width: 576px) {
  .tabs__form {
    padding: 16px;
  }

  .withdraw__title {
    font-size: 16px;
    line-height: 24px;
  }

  .withdraw__sum-container {
    margin-top: 16px;
  }
}