.casting-info {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-template-rows:repeat(3, auto) 1fr;
  grid-template-areas: 
  'date city'
  'price price'
  'title title'
  'tags tags';
}

.casting-info__date {
  margin-top: 0;
  margin-bottom: 16px;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
  grid-area: date;
}

.casting-info__city {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
  grid-area: city;
  justify-self: flex-end;
}

.casting-info__price {
  margin-top: 0;
  margin-bottom: 8px;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #636870;
  grid-area: price;
  display: flex;
  gap: 8px;
  align-items: center;
}

.casting-info__price-icon {
  width: 16px;
  height: 16px;
  background-image: url(../../images/guard-icon.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.casting-info__title {
  margin-top: 0;
  margin-bottom: 16px;
  font-weight: 600;
  font-size: 18px;
  line-height: 26px;
  color: #242A33;
  grid-area: title;
}

.casting-info__tags {
  margin: 0;
  padding: 0;
  display: flex;
  gap: 8px;
  grid-area: tags;
  align-self: flex-end;
}

.casting-info__tag {
  padding: 2px 8px;
  list-style: none;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #636870;
  background-color: #F1F3F6;
  border-radius: 8px;
}

@media screen and (max-width: 576px) {
  .casting-info__date {
    font-size: 10px;
    line-height: 12px;
  }

  .casting-info__city {
    font-size: 10px;
    line-height: 12px;
  }

  .casting-info__tag {
    font-size: 10px;
    line-height: 12px;
  }
}