.multi-select__control {
  padding: 12px 16px !important;
  border: 1px solid #E2E5EA !important;
  border-radius: 16px !important;
  cursor: pointer !important;
}

.multi-select__placeholder {
  color: #999999 !important
}

.multi-select__control--is-focused {
  outline: none;
  border: 1px solid #717171 !important;
  box-shadow: none !important;
}

.multi-select__value-container {
  padding: 0 !important;
}

.multi-select__input-container {
  padding: 0 !important;
  margin: 0 !important;
  
}

.multi-select__indicators {
  display: none !important;
}

.multi-select__menu {
  margin-top: 0 !important;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03) !important;

}

.multi-select__menu-list {
  padding-top: 2px !important;
  padding-bottom: 0 !important;
  border-radius: 8px !important;
}

.multi-select__option--is-focused {
  background-color: #F1F3F6 !important;
}

.multi-select__input:focus {
  border: none !important;
}

.multi-select__option-container {
  display: flex;
  gap: 8px;
  align-items: center;
  flex-direction: row-reverse;
  justify-content: flex-end;
}

.multi-select__option-image {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
}

.multi-select__option-text {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #242A33;
}

.multi-select__multi-value {
  margin: 0 !important;
  margin-right: 4px !important;
  background-color: #D9DCE1 !important;
  border-radius: 6px !important;
  padding: 0 11px;
}

.multi-select__multi-value__label {
  padding: 0 !important;
}

.multi-select__multi-value__remove {
  display: none !important;
}

.multi-select__multi-value__label .multi-select__option-image {
  display: none;
}

.multi-select__multi-value__label .multi-select__option-text {
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  color: #303030;
}

.multi-select__menu-notice--no-options {
  display: none !important;
}