.promo-casting {
  padding: 24px;
  background: #242A33;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
  max-width: 260px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  cursor: pointer;
}

.promo-casting__city {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
}

.promo-casting__price {
  margin: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #A6ADB8;
  display: flex;
  align-items: center;
  gap: 8px;
}

.promo-casting__icon {
  width: 16px;
  height: 16px;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(../../../images/guard-icon.svg);
}

.promo-casting__title {
  margin: 0;
  font-weight: 600;
  font-size: 18px;
  line-height: 26px;
  color: #FFFFFF;
}

.promo-casting__tags {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.promo-casting__tag {
  padding: 2px 8px;
  list-style: none;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #FFFFFF;
  background: #636870;
  border-radius: 8px;
}

@media screen and (max-width: 1110px) {
  .promo-casting {
    padding: 16px;
    max-width: calc(100% - 32px);
  }

  .promo-casting__title {
    font-size: 16px;
    line-height: 24px;
  }
}

@media screen and (max-width: 992px) {
  .promo-casting__city {
    font-size: 10px;
    line-height: 12px;
  }

  .promo-casting__tag {
    font-size: 10px;
    line-height: 12px;
  }
}