.castings-info {
  padding: 24px;
  margin-bottom: 24px;
  width: 100%;
  background: #FFFFFF;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
  display: grid;
  justify-content: space-between;
  gap: 48px;
  grid-template-columns: auto 1fr;
  align-items: center;
}

.castings-info__balance {
  padding: 11px 8px;
  background: #FAFBFD;
  border: 1px solid #F1F3F6;
  border-radius: 8px;
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-template-areas: 
  'coin title button'
  'coin subtitle button';
  align-items: center;
}

.castings-info__balance-icon {
  grid-area: coin;
  margin-right: 24px;
}

.castings-info__balance-title {
  margin: 0;
  grid-area: title;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
}

.castings-info__balance-subtitle {
  margin: 0;
  grid-area: subtitle;
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  color: #242A33;
}

.castings-info__balance-button {
  margin-left: 84px;
  padding: 7px 38px;
  grid-area: button;
  background: #FFFFFF;
  border: 1px solid #E2E5EA;
  border-radius: 16px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  text-decoration: none;
}

.castings-info__balance-button-text {
  margin: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #303030;
}

.castings-info__balance-buton-icon {
  width: 16px;
  height: 16px;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(../../images/balance-button-icon.svg);
}

.castings-info__list {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-between;
}

.castings-info__list-item {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.castings-info__list-title {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
}

.castings-info__list-subtitle {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #242A33;
}

.castings-info__list-subtitle_purple {
  color: #6C26FF;
}

.castings-info__list-subtitle_grey {
  color: #A6ADB8;
}

@media screen and (max-width: 1200px) {
  .castings-info {
    gap: 24px;
  }

  .castings-info__balance-button {
    margin-left: 40px;
    padding: 7px 24px;
  }

  .castings-info__balance-subtitle {
    font-size: 18px;
    line-height: 26px;
  }
}

@media screen and (max-width: 992px) {
  .castings-info {
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 576px) {
  .castings-info {
    gap: 16px;
    padding: 16px;
  }

  .castings-info__balance {
    grid-template-columns: repeat(2, auto);
    grid-template-areas: 
    'title button'
    'subtitle button';
    justify-content: space-between;
  }

  .castings-info__balance-icon {
    display: none;
  }

  .castings-info__balance-title {
    font-size: 10px;
    line-height: 12px;
  }

  .castings-info__balance-buton-icon {
    display: none;
  }

  .castings-info__balance-button {
    margin-left: 0;
    padding: 8px 15px;
  }

  .castings-info__list {
  display: grid;
  grid-template-columns: repeat(2, auto);
  }
}