.popup-contract {
  visibility: hidden;
  display: flex;
  opacity: 0;
  transition: .5s;
  position: fixed;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.popup-contract_opened {
  visibility: visible;
  opacity: 1;
  transition: .5s;
}

.popup-contract__container {
  background-color: #FFFFFF;
  max-width: 75%;
  max-height: 790px;
  padding: 24px 24px 18px;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
  display: grid;
  align-items: center;
  grid-template-columns: repeat(2, auto);
  grid-template-areas: 
  'title button-close'
  'text text'
  'submit-buttons submit-buttons';
}

.popup-contract__title {
  margin: 0;
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  color: #242A33;
  grid-area: title;
}

.popup-contract__text {
  margin-top: 16px;
  margin-bottom: 30px;
  padding-right: 8px;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #636870;
  grid-area: text;
  max-height: 636px;
  overflow: auto;
}

.popup-contract__button_close {
  width: 14px;
  height: 14px;
  padding: 0;
  background: none;
  border: none;
  background-image: url(../../../../images/button-close-popup.svg);
  grid-area: button-close;
  justify-self: flex-end;
  cursor: pointer;
}

.popup-contract__buttons {
  display: flex;
  gap: 12px;
  grid-area: submit-buttons;
}

.popup-contract__button {
  border: none;
  padding: 8px 26px;
  border-radius: 16px;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  cursor: pointer;
}

.popup-contract__button_agree {
  border: none;
  padding: 8px 26px;
  background: #6C26FF;
  box-shadow: 0px 15px 30px rgba(108, 38, 255, 0.15);
  border-radius: 16px;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #FFFFFF;
}

.popup-contract__button_disagree {
  background: #FFFFFF;
  border: 1px solid #E2E5EA;
  color: #303030;
}

@media screen and (max-width: 992px) {
  .popup-contract__container {
    max-width: 100%;
    max-height: 626px;
  }

  .popup-contract__title {
    font-size: 18px;
    line-height: 26px;
  }

  .popup-contract__text {
    max-height: 400px;
    margin-bottom: 24px;
  }

  .popup-contract__buttons {
    flex-direction: column;
    gap: 24px;
  }
}

