.auth {
  display: flex;
}

.auth__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 50vw;
  height: 100vh;
}

@media screen and (max-width: 992px) {
  .auth {
    flex-direction: column;
  }

  .auth__container {
    padding: 24px 16px 64px;
    width: calc(100vw - 32px);
    height: 50vh;
    justify-content: flex-start;
    background-color: #FFFFFF;
  }
}