.header-logo {
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-template-areas: 
  'image title'
  'image subtitle';
  gap: 2px 8px;
}

.header-logo__image {
  grid-area: image;
}

.header-logo__title {
  margin: 0;
  font-weight: 600;
  font-size: 20px;
  line-height: 20px;
  color: #242A33;
}

.header-logo__subtitle {
  margin: 0;
  font-weight: 300;
  font-size: 8px;
  line-height: 10px;
  color: #A6ADB8;
}

@media screen and (max-width: 768px) {
  .header-logo__image {
    width: 28px;
    height: 28px;
  }
}