.textaera {
  display: flex;
  flex-direction: column;
  gap: 8px;
  width: 100%;
  position: relative;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #303030;
}

.textaera__input {
  min-height: 70px;
  padding: 14px 16px;
  border: 1px solid #E2E5EA;
  border-radius: 16px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #303030;
}

.textaera__input::placeholder {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #999999;
}

@media screen and (max-width: 992px) {
  .textaera {
    gap: 4px;
  }
}