.confirm-email {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 50vw;
  height: 100vh;
}

.confirm-email__tick {
  background-image: url(../../../images/Register-tick.svg);
  width: 40px;
  height: 40px;
}

.confirm-email__title {
  margin-top: 52px;
  margin-bottom: 8px;
  font-weight: 500;
  font-size: 18px;
  line-height: 26px;
  color: #242A33;
}

.confirm-email__subtitle {
  margin: 0;
  text-align: center;
  max-width: 366px;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #636870;
}

@media screen and (max-width: 992px) {
  .confirm-email {
    width: 100vw;
  }

  .confirm-email__title {
    margin-top: 28px;
  }
}