.casting-info__container {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.casting-info__price-block {
  display: grid;
  grid-template-columns: 286px 1fr;
  grid-template-rows: repeat(2, auto);
  grid-template-areas: 
  'input checkbox'
  'ratio ratio';
  gap: 8px 16px;
  position: relative;
}

.casting-info__ratio {
  margin-bottom: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #636870;
  grid-area: ratio;
}

.casting-info__place-block {
  display: grid;
  grid-template-columns: 1fr 1fr auto;
  gap: 16px;
}

.casting-info__includes-options {
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-template-rows: auto 1fr;
  grid-template-areas: 
  'title title title'
  'checkbox checkbox checkbox';
  gap: 12px 22px;
}

.casting-info__question-title {
  margin: 0;
  align-self: flex-start;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #303030;
  grid-area: title;
}

.casting-info__gender-block {
  display: flex;
  gap: 16px;
  align-items: center;
}

@media screen and (max-width: 1280px) {
  .casting-info__place-block {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;
  }

  .casting-info__includes-options {
    grid-column-start: 1;
    grid-column-end: 3;
    justify-content: start;
  }
}

@media screen and (max-width: 768px) {
  .casting-info__includes-options {
    grid-template-columns: 1fr;
    grid-template-areas: 
    'title'
    'checkbox';
  }

  .casting-info__place-block {
    grid-template-columns: 1fr;
  }

  .casting-info__includes-options {
    grid-column-start: 1;
    grid-column-end: 2;
  }
}

@media screen and (max-width: 576px) {
  .casting-info__price-block {
    grid-template-columns: 1fr;
    grid-template-areas: 
    'input'
    'ratio'
    'checkbox';
  }
}