.notice {
  list-style: none;
  padding: 16px;
  border-radius: 8px;
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-template-areas: 
  'date notice'
  'text text';
  align-items: center;
}

.notice_new-notice {
  background: #FAFBFD;
}

.notice__date {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
  grid-area: date;
}

.notice__text {
  margin: 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #242A33;
  grid-area: text;
}

.notice__icon {
  display: none;
}

.notice__icon_visible {
  display: block;
  grid-area: notice;
  width: 8px;
  height: 8px;
  background-color: #6C26FF;
  border-radius: 50%;
  justify-self: flex-end;
}

.notice__link {
  text-decoration: none;
  color: rgba(108, 38, 255, 1);
}

@media screen and (max-width: 768px) {
  .notice__date {
    font-size: 10px;
    line-height: 12px;
  }
  
  .notice__text {
    font-size: 14px;
    line-height: 20px;
  }
}