.popup-photos {
  visibility: hidden;
  display: flex;
  opacity: 0;
  transition: .5s;
  position: fixed;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0,0,0,0.5);
}

.popup-photos_opened {
  z-index: 2;
  visibility: visible;
  opacity: 1;
  transition: .5s;
}

.popup-photos__container {
  position: relative;
}

.popup-photos__button_close {
  width: 14px;
  height: 14px;
  padding: 0;
  background: none;
  border: none;
  background-image: url(../../../images/button-close-popup.svg);
  position: absolute;
  top: -24px;
  right: -24px;
  cursor: pointer;
}

.popup-slider {
  width: 100% !important;
  max-width: 1200px !important;
  margin: 0 auto !important;
}

.popup-slider__image {
  max-height: 75vh;
  max-width: 70vw;
  object-fit: cover;
  margin: 0 auto;
}

.popup-slider__arrow {
  width: 30px;
  height: 30px;
  display: block;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  background-image: url(../../../images/arrow-right.svg);
  align-self: center;
  position: absolute;
  top: 50%;
  cursor: pointer;
  z-index: 1;
}

.popup-slider__arrow_next {
  right: 0;
}

.popup-slider__arrow_prev {
  transform: scaleX(-1);
}

.slick-track {
  display: flex;
  align-items: center;
}

@media screen and (max-width: 1400px) {
  .popup-slider {
    max-width: 1000px !important;
  }

  .popup-photos__container {
    max-width: 90vw;
  }

  .popup-slider__arrow_next {
    right: -25px;
  }
  
  .popup-slider__arrow_prev {
    left: -25px;
  }
}

@media screen and (max-width: 1200px) {
  .popup-slider__arrow_next {
    right: 0;
  }
  
  .popup-slider__arrow_prev {
    left:0;
  }

  .popup-photos__button_close {
    right: 0;
  }
}

@media screen and (max-width: 576px) {
  .popup-slider__arrow {
    width: 16px;
    height: 16px;
  }
}