.star-raiting {
  position: relative;
}

.star-raiting::before {
  content: url(../../images/grey-stars.svg);
  display: block;

}

.star-raiting__active {
  position: absolute;
  height: 100%;
  top: 0;
  right: 0;
  transform: rotateY(180deg);
  overflow: hidden;
}

.star-raiting__active::before {
  content: url(../../images/gols-stars.svg);
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.star-raiting__items {
  display: flex;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.star-raiting__item {
  flex: 0 0 20%;
  height: 100%;
  opacity: 0;
}