.profile-promotion__title {
  margin-top: 16px;
  margin-bottom: 24px;
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  color: #242A33;
}

.profile-promotion__form {
  padding: 24px;
  background: #FFFFFF;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
}

.profile-promotion__container {
  padding-bottom: 24px;
  border-bottom: 1px solid #F1F3F6;
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template-rows: auto auto 1fr;
  grid-template-areas: 
  'schema title'
  'schema subtitle'
  'schema inputs';
  gap: 0 24px;
  align-items: center;
}

.profile-promotion__container_promotion {
  padding-top: 24px;
}

.profile-promotion__schema {
  grid-area: schema;
  object-fit: cover;
  width: 225px;
  height: 184px;
}

.profile-promotion__form-title {
  margin-top: 0;
  margin-bottom: 4px;
  font-weight: 600;
  font-size: 18px;
  line-height: 26px;
  color: #242A33;
  grid-area: title;
}

.profile-promotion__form-subtitle {
  margin-top: 0;
  margin-bottom: 16px;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #636870;
  grid-area: subtitle;
}

.profile-promotion__inputs {
  grid-area: inputs;
  display: flex;
  gap: 16px;
}

.profile-promotion__label {
  width: 100%;
  position: relative;
}

.profile-promotion__span {
  position: absolute;
  top: 0;
  right: 12px;
  padding: 4px 8px;
  color: #FFFFFF;
  border-radius: 0px 0px 8px 8px;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
}

.profile-promotion__span_popular {
  background-color: #1BEE6F;
}

.profile-promotion__span_sale {
  background-color: #FF4F9F;
}

.profile-promotion__input_invisible {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.profile-promotion__input_visible {
  display: grid;
  grid-template-rows: auto auto;
  gap: 24px;
  padding: 24px;
  cursor: pointer;
  justify-items: center;
  align-items: center;
  height: calc(100% - 48px);
}

.profile-promotion__input_checked {
  background: linear-gradient(0deg, rgba(123, 97, 255, 0.05), rgba(123, 97, 255, 0.05)), #FFFFFF;
  border: 1px solid #6C26FF;
  border-radius: 12px;
}
.profile-promotion__input_not-checked {
  background: #FFFFFF;
  border: 1px solid #A6ADB8;
  border-radius: 12px;
}

.profile-promotion__input-title {
  margin: 0;
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  color: #242A33;
  text-align: center;
}

.profile-promotion__input-subtitle {
  margin: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #FF4F9F;
}

.profile-promotion__payment-container {
  margin-top: 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.profile-promotion__form-button {
  padding: 14px 82px;
  border: none;
  background: #6C26FF;
  box-shadow: 0px 15px 30px rgba(108, 38, 255, 0.15);
  border-radius: 16px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #FFFFFF;
  cursor: pointer;
}

@media screen and (max-width: 992px) {
  .profile-promotion {
    padding: 24px 24px 48px;
  }

  .profile-promotion__input_visible {
    gap: 16px;
    padding: 24px 16px;
  }

  .profile-promotion__input-title {
    font-size: 18px;
    line-height: 26px;
  }

  .profile-promotion__input-subtitle {
    font-size: 14px;
    line-height: 20px;
  }
}

@media screen and (max-width: 768px) {
  .profile-promotion__title {
    margin-bottom: 16px;
    font-size: 18px;
    line-height: 26px;
  }

  .profile-promotion__inputs {
    flex-direction: column;
  }

  .profile-promotion__schema {
    align-self: flex-start;
  }

  .profile-promotion__payment-container {
    align-items: stretch;
    flex-direction: column;
    gap: 16px;
  }
}

@media screen and (max-width: 576px) {
  .profile-promotion__container {
    grid-template-columns: 1fr;
    grid-template-areas:
    'schema'
    'title'
    'subtitle'
    'inputs';
    justify-items: stretch;
  }

  .profile-promotion__schema {
    width: 100%;
    height: 100%;
  }

  .profile-promotion__form-title {
    margin-top: 16px;
  }
}