.history-item {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.history-item:nth-of-type(2n) {
  background-color: #FAFBFD;;
}

.history-item__value {
  padding: 16px;
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #242A33;
}

.history-item__value_green {
  padding: 16px;
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #1BEE6F;
}

.history-item__value_grey {
  padding: 16px;
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #A6ADB8;
}

@media screen and (max-width: 768px) {
  .history-item {
    background: #FFFFFF;
    box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
    border-radius: 16px;
    margin-bottom: 16px;
    grid-template-columns: repeat(2, auto);
    grid-template-areas: 
    'date status'
    'type sum';
    padding: 16px;
    justify-content: space-between;
    gap: 8px;
  }

  .history-item__value_date {
    grid-area: date;
  }

  .history-item__value_type {
    grid-area: type;
  }

  .history-item__value_sum {
    grid-area: sum;
    justify-self: flex-end;
  }

  .history-item:nth-of-type(2n) {
    background-color: #FFFFFF;
  }

  .history-item__value {
    padding: 0;
  }

  .history-item__value_grey {
    padding: 0;
  }

  .history-item__value_green {
    padding: 0;
  }
}