.table-about__items {
  margin: 0;
  padding: 24px;
  background-color: #FFFFFF;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.table-about__item {
  list-style: none;
}

.table-about__title {
  font-weight: 600;
  color: #242A33;
}

.table-about__title_big {
  font-size: 24px;
  line-height: 30px;
}

.table-about__title_medium {
  font-size: 18px;
  line-height: 26px;
}

.table-about__title_small {
  font-size: 16px;
  line-height: 24px;
}

.table-about__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #636870;
}

@media screen and (max-width: 768px) {
  .table-about__items {
    padding: 16px;
  }

  .table-about__title_big {
    font-size: 18px;
    line-height: 26px;
  }
  
  .table-about__title_medium {
    font-size: 16px;
    line-height: 24px;
  }
  
  .table-about__title_small {
    font-size: 14px;
    line-height: 20px;
  }
  
  .table-about__text {
    font-size: 14px;
    line-height: 20px;
  }
}