.casting-profile__section {
  padding: 24px;
  background: #FFFFFF;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
  align-self: baseline;
}

.casting-profile__section_main-info {
  margin-top: 24px;
  margin-bottom: 24px;
  grid-area: main-info;
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-template-areas: 
  'customer price'
  'title title'
  'info info'
  'description description'
  'tags tags';
}

.casting-profile__price {
  margin: 0;
  font-weight: 600;
  font-size: 18px;
  line-height: 26px;
  color: #242A33;
  grid-area: price;
  display: flex;
  align-items: center;
  gap: 8px;
  justify-self: flex-end;
}

.casting-profile__price-icon {
  width: 24px;
  height: 24px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-image: url(../../../images/guard-icon.svg);
}

.casting-profile__customer {
  grid-area: customer;
  justify-self: flex-start;
  display: grid;
  grid-template-areas: 
  'photo name tick raiting'
  'photo profession profession profession';
  cursor: pointer;
  align-items: center;
}

.casting-profile__customer-photo {
  grid-area: photo;
  margin-right: 8px;
}

.casting-profile__customer-name {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #242A33;
  grid-area: name;
}

.casting-profile__customer-profession {
  margin: 0;
  grid-area: profession;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
}

.casting-profile__customer-tick {
  margin-left: 4px;
  margin-right: 4px;
  grid-area: tick;
  width: 14px;
  height: 14px;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(../../../images/verification-tick.svg);
}

.casting-profile__star-container {
  grid-area: raiting;
  padding: 2px 4px;
  display: flex;
  align-items: center;
  gap: 6px;
  background: #FFF9EA;
  border-radius: 4px;
}

.casting-profile__star-icon {
  width: 12px;
  height: 12px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-image: url(../../../images/Star.svg);
}

.casting-profile__star-value {
  margin: 0;
  font-weight: 600;
  font-size: 10px;
  line-height: 18px;
  color: #242A33;
}

.casting-profile__casting-title {
  margin-top: 8px;
  margin-bottom: 8px;
  grid-area: title;
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  color: #242A33;
}

.casting-profile__info-container {
  margin-bottom: 16px;
  grid-area: info;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #636870;
  display: flex;
}

.casting-profile__date {
  margin: 0;
  padding-right: 8px;
  border-right: 1px solid #D9DCE1;
  margin-right: 8px;
}

.casting-profile__city {
  margin: 0;
}

.casting-profile__description {
  margin-top: 0;
  margin-bottom: 16px;
  padding-top: 16px;
  border-top: 1px solid #F1F3F6;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #242A33;
  grid-area: description;
}

.casting-profile__description-limited {
  margin: 0;
}

.casting-profile__read-more-checker {
  display: none;
}

.casting-profile__tags {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  grid-area: tags;
}

.casting-profile__tag {
  padding: 2px 8px;
  list-style: none;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #636870;
  background-color: #F1F3F6;
  border-radius: 8px;
}

@media screen and (max-width: 992px) {
  .casting-profile__section_main-info {
    margin: 0;
  }
}

@media screen and (max-width: 768px) {
  .casting-profile__section_main-info {
    grid-template-columns: 1fr;
    grid-template-areas:
    'price'
    'customer'
    'title'
    'info'
    'description'
    'tags';
  }

  .casting-profile__price {
    justify-self: flex-start;
    margin-bottom: 16px;
  }

  .casting-profile__casting-title {
    font-size: 18px;
    line-height: 26px;
  }

  .casting-profile__info-container {
    font-size: 14px;
    line-height: 20px;
  }

  .casting-profile__description {
    font-size: 14px;
    line-height: 20px;
  }

  .casting-profile__description-limited {
    margin-bottom: 6px;
    max-height: 200px;
    overflow: hidden;
    position: relative;
  }

  .casting-profile__read-more-checker:checked ~ .casting-profile__description-limited {
    max-height: none;
  }

  .casting-profile__read-more-checker ~ .casting-profile__description-button:before {
    content: "Развернуть";
  }
  .casting-profile__read-more-checker:checked ~ .casting-profile__description-button:before {
    content: "Свернуть";
  }

  .casting-profile__description-button {
    cursor: pointer;
    display: inline-block;
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    color: #6C26FF;
  }

  .casting-profile__tag {
    font-size: 10px;
    line-height: 12px;
  }
}

@media screen and (max-width: 576px) {
  .casting-profile__section {
    padding: 16px;
  }
}