.casting-profile {
  display: grid;
  align-items: center;
  grid-template-columns: auto auto;
}

.casting-profile_without_warning {
  grid-template-rows: repeat(3, auto) 1fr;
  grid-template-areas: 
  'button-back buttons'
  'main-info main-info'
  'secondary-information secondary-information';
}

.casting-profile_with_warning {
  grid-template-rows: repeat(4, auto) 1fr;
  grid-template-areas: 
  'button-back buttons'
  'main-info main-info'
  'warning warning'
  'secondary-information secondary-information';
}

.casting-profile__buttons {
  display: flex;
  justify-content: flex-end;
  gap: 16px;
  grid-area: buttons;
}

.casting-profile__button {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.casting-profile__cast-success {
  margin: 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #1BEE6F;
  display: flex;
  justify-content: center;
  gap: 10px;
}

.casting-profile__cast-success-icon {
  width: 20px;
  height: 20px;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(../../images/cast-success-icon.svg);
}

.casting-profile__button_chat {
  border: none;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #242A33;
  display: flex;
  justify-content: center;
  align-items: center;
}

.casting-profile__button-icon_chat {
  margin-right: 10px;
  width: 24px;
  height: 24px;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(../../images/double-chat-icon.svg);
}

.casting-profile__number-notice {
  margin-left: 4px;
  width: 16px;
  height: 16px;
  font-weight: 500;
  font-size: 10px;
  line-height: 12px;
  color: #FFFFFF;
  background: #FF4F9F;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.casting-profile__button-icon_cansel {
  width: 16px;
  height: 16px;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(../../images/cansel-button-icon.svg);
}

.casting-profile__button_cansel {
  padding: 7px 16px;
  border: 1px solid #E2E5EA;
  border-radius: 16px;
  background: #FFFFFF;
  border: 1px solid #E2E5EA;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #303030;
  display: flex;
  gap: 8px;
  align-items: center;
  justify-content: center;
}

.casting-profile__button_go-room {
  padding: 8px 16px;
  border: none;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  background: #6C26FF;
  border-radius: 16px;
  box-shadow: 0px 15px 30px rgba(108, 38, 255, 0.15);
  color: #FFFFFF;
}

.casting-profile__cast-price {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #636870;
  display: flex;
  align-items: center;
  gap: 4px;
}

.casting-profile__cast-price-icon {
  width: 16px;
  height: 16px;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(../../images/info-icon.svg);
}

.casting-profile__button_confirm {
  padding: 8px 16px;
  border: none;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  background: #6C26FF;
  border-radius: 16px;
  box-shadow: 0px 15px 30px rgba(108, 38, 255, 0.15);
  color: #FFFFFF;
}

.casting-profile__cast-passed {
  margin: 0;
  padding: 8px 16px;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #FFFFFF;
  background: #D9DCE1;
  border-radius: 16px;
  text-align: center;
}

.casting-profile__cast-arbitration {
  margin: 0;
  padding: 8px 16px;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #FFFFFF;
  background: #FF1F1F;
  border-radius: 16px;
  text-align: center;
}

.casting-profile__link {
  text-decoration: none;
  grid-column-start: 1;
  grid-column-end: 3;
}

.casting-profile__warning-container {
  grid-area: warning;
}

@media screen and (max-width: 992px) {
  .casting-profile {
    display: grid;
    align-items: center;
    grid-template-columns: 1fr;
    gap: 26px;
  }

  .casting-profile_without_warning {
    grid-template-rows: repeat(3, auto) 1fr;
    grid-template-areas: 
    'button-back'
    'main-info'
    'secondary-information'
    'buttons';
  }
  
  .casting-profile_with_warning {
    grid-template-rows: repeat(4, auto) 1fr;
    grid-template-areas: 
    'button-back'
    'main-info'
    'warning'
    'secondary-information'
    'buttons';
  }

  .casting-profile__warning-container {
    margin-bottom: -24px;
  }

  .casting-profile__buttons {
    justify-content: flex-start;
    align-self: flex-start;
  }

  .casting-profile__link {
    text-decoration: none;
    grid-column-start: 1;
    grid-column-end: 2;
  }
}

@media screen and (max-width: 768px) {
  .casting-profile {
    gap: 16px;
  }

  .casting-profile__buttons {
    flex-direction: column;
  }
}
