.new-casting__title {
  margin-top: 16px;
  margin-bottom: 24px;
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  color: #242A33;
}

.new-casting__form {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  gap: 24px;
}

.new-casting__form-container {
  padding: 24px;
  background: #FFFFFF;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
}

.new-casting__button {
  padding: 14px 49.5px;
  border: none;
  background: #6C26FF;
  box-shadow: 0px 15px 30px rgba(108, 38, 255, 0.15);
  border-radius: 16px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #FFFFFF;
  cursor: pointer;
  grid-column-start: 2;
  grid-column-end: 3;
}

.new-casting__container {
  display: grid;
  grid-template-columns: 1fr auto;
}

.new-casting__choose-actor-text {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #636870;
}

@media screen and (max-width: 768px) {
  .new-casting__container {
    display: flex;
    flex-direction: column;
    gap: 24px;
  }
}

@media screen and (max-width: 576px) {
  .new-casting__title {
    font-size: 18px;
    line-height: 26px;
  }

  .new-casting__form-container {
    padding: 16px;
  }
}