.switch {
  margin: 0;
  display: grid;
  grid-template-columns: auto 1fr;
  align-items: center;
  gap: 12px;
}

.switch_type_invisible {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.switch_type_visible {
  display: inline-block;
  vertical-align: middle;
  width: 36px;
  height: 20px;
  border: none;
  cursor: pointer;
}

.switch_type_invisible:checked + .switch_type_visible {
  background-image: url(../../images/checked.svg);
}

.switch_type_invisible:not(:checked) + .switch_type_visible{
  background-image: url(../../images/unchecked.svg);
}

.switch__text {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #303030;
}