.footer-logo {
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-template-areas: 
  'image title'
  'image subtitle';
  gap: 2px 8px;
}

.footer-logo__image {
  grid-area: image;
}

.footer-logo__title {
  margin: 0;
  font-weight: 600;
  font-size: 20px;
  line-height: 20px;
  color: #D9DCE1;
}

.footer-logo__subtitle {
  margin: 0;
  font-weight: 300;
  font-size: 8px;
  line-height: 10px;
  color: #D9DCE1;;
}