.reviews__list {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.reviews__title {
  margin-top: 16px;
  margin-bottom: 24px;
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  color: #242A33;
}

.reviews__button_load-more {
  padding: 12px 20px;
  border: 1px solid #E2E5EA;
  border-radius: 16px;
  background: #FFFFFF;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #303030;
  align-self: center;
  cursor: pointer;
}

@media screen and (max-width: 576px) {
  .reviews__button_load-more {
    align-self: auto;
  }
}

@media screen and (max-width: 576px) {
  .reviews__title {
    margin-bottom: 20px;
    font-size: 18px;
    line-height: 26px;
  }
}