.menu-profile {
  position: absolute;
  z-index: 2;
  padding: 24px;
  background: #FFFFFF;
  border: 1px solid #F1F3F6;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
  top: 46px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.menu-profile_invisible {
  display: none;
}

.menu-profile__list {
  margin: 0;
  padding: 0;
  padding-bottom: 16px;
  border-bottom: 1px solid #F1F3F6;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.menu-profile__item {
  list-style: none;
}

.menu-profile__link {
  display: flex;
  text-decoration: none;
  gap: 16px;
  color: #636870;
  position: relative;
}

.menu-profile__link_notifications {
  border: none;
  background-color: transparent;
  padding: 0;
  cursor: pointer;
}

.menu-profile__link_active {
  font-weight: 500;
  color: #303030;
}

.menu-profile__link:hover {
  color: #303030;
  font-weight: 500;
}

.menu-profile__text {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
}

.menu-profile__button {
  padding: 0;
  border: none;
  background-color: transparent;
}

.menu-profile__button_exit {
  margin-top: 16px;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #636870;
  cursor: pointer;
}

.menu-profile__button_exit:hover {
  color: #303030;
}

.menu-profile__button_close {
  display: none;
}

.menu-profile__notice {
  position: absolute;
  border-radius: 50%;
  width: 10px;
  height: 10px;
  background: #FF4F9F;
  bottom: -5px;
  left: 27px;
}

.menu-profile__notice_invisible {
  display: none;
}

.menu-profile__button_new-cast {
  margin-top: 16px;
  padding: 12px 20px;
  text-decoration: none;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #FFFFFF;
  background: #6C26FF;
  box-shadow: 0px 15px 30px rgba(108, 38, 255, 0.15);
  border-radius: 16px;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .menu-profile {
    border-radius: 0;
    padding: 0;
    position: fixed;
    top: 0;
    right: 0;
    width: 100vw;
    height: 100vh;
    align-items: flex-start;
    justify-content: space-between;
  }

  .menu-profile__item_notifications {
    display: none;
  }

  .menu-profile__list {
    gap: 40px;
    width: 100%;
    height: 100%;
    padding: 0 16px;
    border-bottom: none;
  }

  .menu-profile__button_close {
    display: block;
    margin: 16px 0 53px 16px;
    background-position: center;
    background-repeat: no-repeat;
    width: 24px;
    height: 24px;
    background-image: url(../../../images/close-profile-menu-button.svg);
  }

  .menu-profile__button_exit {
    padding: 24px 0 16px 16px;
    margin: 0;
    font-size: 16px;
    line-height: 24px;
    border-top: 1px solid #F1F3F6;
    width: 100%;
  }

  .menu-profile__text {
    font-size: 16px;
    line-height: 24px;
  }

  .menu-profile__icon {
    width: 48px;
    height: 48px;
  }

  .menu-profile__notice {
    bottom: 0;
    left: 38px;
  }

  .menu-profile__button_new-cast {
    width: calc(100% - 64px);
    margin: 0 16px 24px 16px;
    text-align: center;
  }
}