.subscribe-warning {
  display: flex;
  margin-bottom: 24px;
  padding: 24px;
  background: #FFF8E4;
  border: 1px solid #FFC224;
  border-radius: 16px;
  align-items: center;
  gap: 180px;
}
.subscribe-warning__container {
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 24px;
  align-items: center;
}

.subscribe-warning__icon {
  width: 44px;
  height: 44px;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(../../images/warning-icon.svg);
}

.subscribe-warning__text {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #000000;
}

.subscribe-warning__button {
  padding: 12px 20px;
  background: #6C26FF;
  text-decoration: none;
  box-shadow: 0px 15px 30px rgba(108, 38, 255, 0.15);
  border-radius: 16px;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #FFFFFF;
  cursor: pointer;
  white-space: nowrap;
  text-align: center;
}

@media screen and (max-width: 1200px) {
  .subscribe-warning {
    gap: 80px;
  }
}

@media screen and (max-width: 992px) {
  .subscribe-warning {
    flex-direction: column;
    gap: 24px;
    align-items: flex-start;
  }
}

@media screen and (max-width: 576px) {
  .subscribe-warning {
    padding: 16px;
    align-items: stretch;
  }

  .subscribe-warning__container {
    gap: 16px;
    grid-template-columns: 1fr
  }
}