.main-info_customer {
  display: grid;
  grid-template-columns: 335px 1fr;
  grid-template-rows: 1fr;
}

.main-info__photo-container {
  position: relative;
  background: linear-gradient(0deg, #E8EBEF, #E8EBEF);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.main-info__photo-icon {
  width: 50px;
  height: 50px;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(../../../images/profile-photo-icon.svg);
}

.main-info__photo-input_invisible {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.main-info__photo-input_visible {
  width: 24px;
  height: 24px;
  cursor: pointer;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(../../../images/profile-image-icon.svg);
}

.main-info__photo-label {
  position: absolute;
  display: flex;
  bottom: 15px;
  right: 15px;
}

@media screen and (max-width: 992px) {
  .main-info_customer {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 283px auto;
  }

  .main-info__photo-container {
    max-width: 296px;
  }
}

@media screen and (max-width: 576px) {
  .main-info__photo-container {
    max-width: 100%;
  }
}