.mobile-menu {
  background: #FFFFFF;
  border-radius: 0;
  padding: 0;
  position: fixed;
  top: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-direction: column;
  z-index: 3;
}

.mobile-menu_invisible {
  display: none;
}

.mobile-menu__button {
  padding: 0;
  border: none;
  background-color: transparent;
}

.mobile-menu__button_close {
  margin: 53px 53px 0 0;
  align-self: end;
  display: block;
  background-position: center;
  background-repeat: no-repeat;
  width: 24px;
  height: 24px;
  background-image: url(../../../images/close-profile-menu-button.svg);
}

.mobile-menu__list {
  margin: 0;
  padding: 0;
  padding-top: 72px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
  width: 100%;
  height: 100%;
}

.mobile-menu__item {
  list-style: none;
}

.mobile-menu__link {
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #636870;
  text-decoration: none;
}

.mobile-menu__link_active {
  font-weight: 500;
  color: #303030;
}

@media screen and (max-width: 576px) {
  .mobile-menu__button_close {
    margin: 24px 24px 0 0;
  }
}