.questionary__customer-container {
  display: grid;
  gap: 16px;
}

.questionary__customer-container_name {
  margin-bottom: 16px;
  grid-template-columns: 1fr 1fr 1fr;
}

.questionary__customer-container_company {
  margin-bottom: 16px;
  gap: 32px;
  grid-template-columns: auto 1fr
}

@media screen and (max-width: 992px) {
  .questionary__customer-container_company {
    gap: 16px;
  }
  .questionary__customer-container {
    display: flex;
    flex-direction: column;
  }
}