.filter-radio {
  margin-bottom: 16px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.filter-radio__title {
  margin-bottom: 8px;
  margin-top: 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #303030;
}

.filter-radio__title_display_none {
  display: none;
}

.filter-radio__container {
  display: flex;
  gap: 12px;
  position: relative;
}

.filter-radio__container_component_register {
  padding: 0;
  justify-content: center;
  margin-bottom: 48px;
  gap: 32px;
}

.filter-radio__label {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 12px;
}

.filter-radio__label-subtitle {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #636870;
}

.filter-radio__label-subtitle_component_register {
  font-size: 16px;
  line-height: 24px;
}

.filter-radio__input_invisible {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.filter-radio__input_visible {
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  border: none;
  cursor: pointer;
}

.filter-radio__input_invisible:checked + .filter-radio__input_visible {
  background-image: url(../../images/input-checked.svg);
}

.filter-radio__input_invisible:not(:checked) + .filter-radio__input_visible {
  background-image: url(../../images/input-unchecked.svg);
}

@media screen and (max-width: 992px) {
  .filter-radio__container_component_register {
    margin-bottom: 40px;
  }
}