.my-castings__title {
  margin-top: 16px;
  margin-bottom: 32px;
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  color: #242A33;
}

.my-castings__list {
  margin-top: 32px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px
}

.my-castings__button-load-more {
  padding-top: 12px;
  padding-bottom: 12px;
  display: none;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #303030;
  background: #FFFFFF;
  border: 1px solid #E2E5EA;
  border-radius: 16px;
  cursor: pointer;
}

@media screen and (max-width: 992px) {
  .my-castings__list {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  .my-castings__button-load-more {
    display: block;
    align-self: center;
    padding-right: 115px;
    padding-left: 115px;
  }
}

@media screen and (max-width: 576px) {
  .my-castings__title {
    font-size: 18px;
    line-height: 26px;
  }
  .my-castings__button-load-more {
    display: block;
    align-self: auto;
    padding-right: 0;
    padding-left: 0;
  }
}