.question-list__form {
  padding: 24px;
  background: #FFFFFF;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
}

.question-list__buttons {
  margin-top: 48px;
  display: flex;
  justify-content: space-between;
}

.question-list__buttons_step_first {
  justify-content: flex-end;
}

.question-list__button {
  border: none;
  padding: 14px 95px;
  border-radius: 16px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  cursor: pointer;
}

.question-list__button_back {
  background-color: transparent;
  border: 1px solid #D9DCE1;
  color: #303030;
}

.question-list__button_next {
  background: #6C26FF;
  box-shadow: 0px 15px 30px rgba(108, 38, 255, 0.15);
  color: #FFFFFF;
}

.question-list__button_display_none {
  display: none;
}

@media screen and (max-width: 992px) {
  .question-list__form {
    padding: 16px;
  }

  .question-list__buttons {
    flex-direction: column-reverse;
    margin-top: 24px;
  }

  .question-list__button:first-of-type {
    margin-top: 24px;
  }
}