.questionary__photo-block {
  margin-bottom: 36px;
  justify-content: stretch;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(4, auto);
  grid-template-areas: 
  'title-dressed title-dressed title-dressed title-undressed title-undressed title-undressed'
  'photo-face-dressed photo-profile-dressed photo-behind-dressed photo-face-undressed photo-profile-undressed photo-behind-undressed'
  'subtitle-photo subtitle-photo subtitle-photo subtitle-video subtitle-video subtitle-video'
  'photos photos photos videos videos videos';
  gap: 16px 16px
}

.questionary__photo-title {
  margin: 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #303030;
}

.questionary__photo-title_dressed {
  grid-area: title-dressed;
}

.questionary__photo-title_undressed {
  grid-area: title-undressed;
}

.questionary__photo-item {
  height: 150px;
  background: #FAFBFD;
  border: 1px dashed #A6ADB8;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 12px;
  cursor: pointer;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}

.questionary__photo-item_invisible {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.questionary__photo-description {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  text-align: center;
  color: #636870;
}

.questionary__photo-icon {
  width: 24px;
  height: 24px;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(../../images/photo-icon.svg);
}

.questionary__photo-item_face {
  grid-area: photo-face-dressed;
}

.questionary__photo-item_profile {
  grid-area: photo-profile-dressed;

}

.questionary__photo-item_behind {
  grid-area: photo-behind-dressed;
}

.questionary__photo-item_face-undressed {
  grid-area: photo-face-undressed;
}

.questionary__photo-item_profile-undressed {
  grid-area: photo-profile-undressed;
}

.questionary__photo-item_behind-undressed {
  grid-area: photo-behind-undressed;
}

.questionary__photo-subtitle {
  margin-top: 8px;
  margin-bottom: 0;
  grid-area: subtitle-photo;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #303030;
}

.questionary__video-subtitle {
  margin-top: 8px;
  margin-bottom: 0;
  grid-area: subtitle-video;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #303030;
}

.questionary__photo-item_photos {
  grid-area: photos;
}

.questionary__photo-item_videos {
  grid-area: videos;
}

.questionary__photo-label {
  position: relative;
}

.questionary__photo-button_delete {
  padding: 0;
  position: absolute;
  width: 32px;
  height: 32px;
  border: none;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 8px;
  cursor: pointer;
  background-image: url(../../images/delete-photo-button.svg);
  background-repeat: no-repeat;
  background-position: center;
  right: 8px;
  bottom: 8px;
}

.questionary__photo-load {
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #5915E8;
}

@media screen and (max-width: 992px) {
  .questionary__photo-block {
    grid-template-columns: repeat(3, 1fr);
    grid-template-areas: 
    'title-dressed title-dressed title-dressed'
    'photo-face-dressed photo-profile-dressed photo-behind-dressed'
    'title-undressed title-undressed title-undressed'
    'photo-face-undressed photo-profile-undressed photo-behind-undressed'
    'subtitle-photo subtitle-photo subtitle-photo'
    'photos photos photos'
    'subtitle-video subtitle-video subtitle-video'
    'videos videos videos';
    gap: 22px 16px;
  }
}