.finance__title {
  margin-top: 16px;
  margin-bottom: 38px;
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  color: #242A33;
}

.finance__container {
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-template-areas: 
  'button finance'
  'title finance';
  justify-content: space-between;
  align-items: center;
}

.finance__balance {
  padding: 19px 16px;
  background: #FFFFFF;
  border: 1px solid #F1F3F6;
  border-radius: 16px;
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-template-areas: 
  'coin title'
  'coin subtitle';
  align-items: center;
  grid-area: finance;
  align-self: flex-start;
}

.finance__balance-icon {
  grid-area: coin;
  margin-right: 24px;
}

.finance__balance-title {
  margin: 0;
  grid-area: title;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
}

.finance__balance-subtitle {
  margin: 0;
  grid-area: subtitle;
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  color: #242A33;
}

@media screen and (max-width: 768px) {
  .finance__container {
    margin-bottom: 32px;
    grid-template-columns: 1fr;
    grid-template-areas: 
    'button'
    'title'
    'finance';
    justify-items: flex-start;
  }

  .finance__title {
    margin-bottom: 16px;
  }

  .finance__balance {
    grid-template-columns: auto 1fr;
    justify-self: stretch;
  }
}

@media screen and (max-width: 768px) {
}

@media screen and (max-width: 576px) {
  .finance__title {
    font-size: 18px;
    line-height: 26px;
  }
}