.promo {
  grid-area: advertising;
}

.promo__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: stretch;
}

.promo__list {
  list-style: none;
}

.promo__subtitle {
  margin-top: 20px;
  margin-bottom: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #828D9E;
  text-align: center;
}

@media screen and (max-width: 992px) {
  .promo {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-area: auto;
  }
}

@media screen and (max-width: 768px) {
  .promo__subtitle {
    display: none;
  }
}