.castings {
  grid-area: castings;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.castings-table {
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: auto;
  gap: 20px 20px;
}

.castings-table__item {
  padding: 24px;
  margin: 0;
  list-style: none;
  background: #FFFFFF;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
  cursor: pointer;
  display: flex
}

.castings-table__item_banner {
  list-style: none;
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 3;
  grid-row-end: 4;
}

.castings-table__button {
  margin-top: 20px;
  padding: 12px 20px;
  border: none;
  background-color: #FFFFFF;
  border: 1px solid #E2E5EA;
  border-radius: 16px;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #303030;
  align-self: center;
  cursor: pointer;
}

@media screen and (max-width: 1110px) {
  .castings-table {
    grid-template-columns: 1fr;
  }

  .castings-table__item_banner {
    grid-column-start: 1;
    grid-column-end: 2;
  }
}

@media screen and (max-width: 768px) {
  .castings-table {
    gap: 16px;
  }
}