.transfer__container {
  display: grid;
  grid-template-columns: 368px 368px;
  gap: 24px;
  border-bottom: 1px solid #F1F3F6;;
  padding-bottom: 50px;
}

.transfer__text {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #999999;
  position: absolute;
  bottom: -20px;
}

.transfer__button {
  margin-top: 24px;
  padding: 14px 76.5px;
  border: none;
  background: #6C26FF;
  box-shadow: 0px 15px 30px rgb(108 38 255 / 15%);
  border-radius: 16px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #FFFFFF;
  cursor: pointer;
}

@media screen and (max-width: 992px) {
  .transfer__container {
    grid-template-columns: 1fr;
  }
}