.questionary__questions {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: stretch;
  gap: 16px;
  width: 100%;
  margin-bottom: 36px;
}

.questionary__questions_invisible {
  display: none;
}

.questionary__questions_step_first {
  padding-bottom: 58px;
  border-bottom: 1px solid #D9DCE1;;
}

.questionary__container {
  display: grid;
  gap: 16px;
}

.questionary__container_general-info {
  grid-template-columns: 1fr 1fr 1fr minmax(100px, 160px);
}

.questionary__container_contacts {
  grid-template-columns: 2fr 1fr;
}

.questionary__container_education {
  grid-template-columns: 1fr auto;
}

.questionary__switches {
  display: flex;
  gap: 32px;
}

.input_number {
  padding-left: 36px;
}

.input__placeholder{
  position: absolute;
  left: 16px;
  top: 45px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #303030;
}

.input__placeholder_nonfocus {
  color: rgba(153, 153, 153, 1);
}

.input_number {
  padding-left: 36px;
}

@media screen and (max-width: 992px) {
  .questionary__questions_step_first {
    padding-bottom: 26px;
  }

  .questionary__container {
    display: flex;
    flex-direction: column;
  }

  .questionary__switches {
    margin-top: 22px;
    flex-direction: column;
    gap: 30px;
  }
}