.theme-list {
  margin: 0;
  margin-bottom: 24px;
  padding: 0;
  display: flex;
  gap: 24px;
  flex-direction: row;
  flex-wrap: wrap;
  position: relative;
}

.theme-list__item {
  list-style: none;
}

.theme-list__input_invisible {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.theme-list__input_visible {
  padding: 8px 16px;
  display: inline-block;
  vertical-align: middle;
  border: 1px solid #D9DCE1;
  border-radius: 8px;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #636870;
  cursor: pointer;
}

.theme-list__input_invisible:checked + .theme-list__input_visible {
  background: linear-gradient(0deg, rgba(108, 38, 255, 0.03), rgba(108, 38, 255, 0.03)), #FFFFFF;
  border: 1px solid #6C26FF;
}

.theme-list__input_invisible:not(:checked) + .theme-list__input_visible {
  border: 1px solid #D9DCE1;
  background: transparent;
}

@media screen and (max-width: 992px) {
  .theme-list {
    gap: 8px;
  }

  .theme-list__input_visible {
    font-size: 12px;
    line-height: 18px;
  }
}