.footer {
  background-color: #242A33;
}

.footer__wrapper {
  max-width: 1396px;
  margin: 0 auto;
  padding: 24px 131px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer_profile-menu {
  width: 100%;
}

.footer__links {
  display: flex;
  gap: 40px;
}

.footer__link {
  text-decoration: none;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #636870;
}

@media screen and (max-width: 1700px) {
  .footer__wrapper {
    padding: 24px 100px;
  }
}

@media screen and (max-width: 1400px) {
  .footer__wrapper {
    padding: 24px 60px;
  }
}

@media screen and (max-width: 870px) {
  .footer__wrapper {
    padding: 32px 16px;
    flex-direction: column;
  }

  .footer__links {
    margin-top: 40px;
    flex-direction: column;
    align-items: center;
    gap: 16px;
  }

  .footer__link {
    font-size: 12px;
    line-height: 18px;
  }
}