.content_actor-profile {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  align-content: flex-start;
}

.profile__title {
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  color: #242A33;
}

.profile__buttons {
  display: flex;
  gap: 16px;
}

.profile__button {
  padding: 8px 16px;
  border: none;
  background-color: transparent;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  border-radius: 16px;
  cursor: pointer;
  text-decoration: none;
}

.profile__button_edit {
  background: #FFFFFF;
  border: 1px solid #E2E5EA;
  display: flex;
  gap: 8px;
  color: #303030;
}

.profile__button-icon {
  width: 16px;
  height: 16px;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(../../images/pencil.svg);
}

.profile__button-text {
  margin: 0;
}

.profile__button_verification {
  background: #6C26FF;
  box-shadow: 0px 15px 30px rgba(108, 38, 255, 0.15);
  color: #FFFFFF;
}

@media screen and (max-width: 768px) {
  .profile__button-text {
    display: none
  }

  .profile__button_verification {
    padding: 0;
    width: 44px;
    height: 46px;
    background-image: url(../../images/crown-icon.svg);
    background-repeat: no-repeat;
    background-position: center;
  }

  .profile__button_edit {
    padding: 0;
    width: 44px;
    height: 46px;
    align-items: center;
    justify-content: center;
  }
}