.questionary__questions_step_second {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

@media screen and (max-width: 992px) {
  .questionary__questions_step_second {
    padding-top: 32px;
    display: flex;
    flex-direction: column;
  }

  .questionary__question_without-border {
    border-bottom: none !important;
  }
}