.filter-checkbox {
  display: flex;
  gap: 12px;
  align-self: center;
}

.filter-checkbox_use-grant {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template-rows: auto auto;
  grid-template-areas: 
  'checkbox title'
  'checkbox link';
  gap: 0 12px;
  align-items: center;
  align-content: center;
  align-self: end;
}

.filter-checkbox__title {
  margin: 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #303030;
  grid-area: title;
}

.filter-checkbox_invisible {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.filter-checkbox_visible {
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  border: none;
  grid-area: checkbox;
  cursor: pointer;
}

.filter-checkbox_invisible:checked + .filter-checkbox_visible {
  background-image: url(../../images/purple-tick.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: inherit;
  border: none !important;
}

.filter-checkbox_invisible:not(:checked) + .filter-checkbox_visible {
  background-image: none;
  border: 1px solid #E2E5EA;
  border-radius: 4px;
  width: 18px;
  height: 18px;
}

.filter-checkbox_invisible:focus-visible+ .filter-checkbox_visible {
  border: 1px solid #717171;
  border-radius: 4px;
  outline: none;
}

.filter-checkbox__link {
  margin-top: 2px;
  text-decoration: none;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #6C26FF;
  grid-area: link;
}