.add-money__ratio {
  margin-top: 0;
  margin-bottom: 24px;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #636870;
  display: flex;
  gap: 4px;
}

.add-money__ratio-icon {
  width: 16px;
  height: 16px;
  background-image: url(../../../images/info-icon.svg);
  background-repeat: no-repeat;
  background-position: center;
}

.add-money__container {
  padding-bottom: 24px;
  display: grid;
  grid-template-columns: 400px auto;
  gap: 24px;
  border-bottom: 1px solid #F1F3F6;
}

.add-money__card-data {
  max-width: 548px;
  margin-top: 24px;
  padding: 24px;
  background: #FAFBFD;
  border: 1px solid #F1F3F6;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.add-money__card-data-container {
  display: grid;
  gap: 16px;
  grid-template-columns: calc(68% - 48px) 16% 16%;
}

.add-money__label {
  position: relative;
}

.add-money__label_card-number {
  position: relative;
  grid-column-start: 1;
  grid-column-end: 4;
}

.add-money__input {
  width: calc(100% - 32px);
  margin: 0;
  padding: 12px 16px;
  border: 1px solid #E2E5EA;
  border-radius: 16px;
  background-color: #FFFFFF;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #303030;
}

.add-money__input::placeholder {
  color: #999999;
}

.add-money__button {
  margin-top: 24px;
  padding: 14px 76.5px;
  border: none;
  background: #6C26FF;
  box-shadow: 0px 15px 30px rgba(108, 38, 255, 0.15);
  border-radius: 16px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #FFFFFF;
  cursor: pointer;
}

@media screen and (max-width: 992px) {
  .add-money__card-data {
    max-width: 100%;
  }

  .add-money__container {
    display: flex;
    flex-direction: column;
  }

  .add-money__card-data-container {
    grid-template-columns: 1fr;
  }

  .add-money__label_card-number {
    position: relative;
    grid-column-start: 1;
    grid-column-end: 3;
  }

  .add-money__button {
    width: 100%;
  }
}

@media screen and (max-width: 768px) {
  .add-money__card-data {
    padding: 8px;
  }

  .add-money__button {
    margin-top: 16px;
    font-size: 14px;
    line-height: 20px;
  }
}