.content {
  max-width: 1396px;
  width: calc(100vw - 260px);
  margin: 0 auto;
  padding: 24px 130px 48px;
  flex: 1;
}

.content_actors {
  display: grid;
  grid-template-columns: auto 1fr auto;
  grid-template-rows: auto auto 1fr;
  grid-template-areas: 
  'heading heading heading'
  'filter filter filter'
  'priveleged-actors actors advertising';
  gap: 0 20px;
}

.actors__container {
  display: flex;
  justify-content: space-between;
  grid-area: heading;
}

.actors__heading {
  display: flex;
  flex-direction: column;
}

.actors__title {
  margin-top: 0;
  margin-bottom: 4px;
  font-weight: 600;
  font-size: 32px;
  line-height: 38px;
  color: #242A33;
}

.actors__subtitle {
  margin-top: 0;
  margin-bottom: 24px;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
}

@media screen and (max-width: 1700px) {
  .content {
    width: calc(100vw - 200px);
    padding: 24px 100px 48px;
  }
}

@media screen and (max-width: 1620px) {
  .content_actors {
    grid-template-columns: auto 1fr;
    grid-template-areas: 
    'heading heading'
    'filter filter'
    'priveleged-actors actors';
  }
}

@media screen and (max-width: 1400px) {
  .content {
    width: calc(100vw - 120px);
    padding: 24px 60px 48px;
  }
}

@media screen and (max-width: 1110px) {
  .content_actors {
    grid-template-columns: 20% 1fr;
  }
}

@media screen and (max-width: 992px) {
  .content_actors {
    grid-template-columns: auto;
    grid-template-areas: 
    'heading'
    'filter'
    'actors';
  }
}

@media screen and (max-width: 768px) {
  .content {
    width: calc(100vw - 32px);
    padding: 16px 16px 48px;
    position: relative;
  }
  .actors__title {
    font-size: 24px;
    line-height: 30px;
  }

  .actors__subtitle {
    margin: 0;
    font-size: 10px;
    line-height: 12px;
  }

  .actors__container {
    margin-bottom: 16px;
    flex-direction: column;
    align-items: stretch;
    gap: 14px;
    position: relative;
  }

  .actors__heading {
    grid-template-areas: heading;
  }
}