.greetings {
  width: 50vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #FAFBFD;
}

.greetings__container {
  padding-left: 32px;
  padding-right: 32px;
  max-width: 477px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.greetings__title {
  margin: 96px 0 16px;
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  color: #242A33;
  text-align: center;
}

.greetings__subtitle {
  margin: 0;
  font-weight: 400;
  font-size: 18px;
  line-height: 26px;
  color: #636870;
  text-align: center;
}

.greetings__image {
  width: 100%;
}

@media screen and (max-width: 992px) {
  .greetings {
    width: 100vw;
    height: 50vh;
    background-color: #FFFFFF;
  }

  .greetings__container {
    padding: 64px 16px 0;
  }

  .greetings__image {
    width: 200px;
    height: 200px;
  }
  
  .greetings__title {
    margin: 24px 0 8px;
  }
}

@media screen and (max-width: 768px) {
  .greetings__title {
    margin: 24px 0 8px;
    font-size: 18px;
    line-height: 26px;
  }

  .greetings__subtitle {
    max-width: 332px;
    font-size: 16px;
    line-height: 24px;
  }
}