.general-info {
  padding: 24px;
  width: 100%;
  background: #FFFFFF;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
  display: flex;
  flex-direction: column;
}

.general-info__table-title {
  margin-top: 0;
  margin-bottom: 16px;
  font-weight: 600;
  font-size: 18px;
  line-height: 26px;
  color: #303030;
}

.general-info__table {
  margin: 0;
  padding: 0;
  display: grid;
  gap: 16px;
}

.general-info__table_body {
  margin-bottom: 24px;
  grid-template-columns: repeat(4, auto);
}

.general-info__table_interests {
  grid-template-columns: repeat(3, auto);
}

.general-info__table-item {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.general-info__table-item-title {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
}

.general-info__table-item-subtitle {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #242A33;
}

@media screen and (max-width: 768px) {
  .general-info__table_body {
    grid-template-columns: repeat(2, auto);
  }

  .general-info__table_interests {
    grid-template-columns: repeat(2, auto);
  }

  .general-info__table-title {
    font-size: 16px;
    line-height: 24px;
  }

  .general-info__table-item-title {
    font-size: 12px;
    line-height: 18px;
  }
}

@media screen and (max-width: 576px) {
  .general-info__table {
    justify-content: space-between;
  }
  
  .general-info {
    padding: 16px;
  }

  .general-info__table-item_big {
    grid-column-start: 1;
    grid-column-end: 3;
  }
}