.verification__title {
  margin-top: 16px;
  margin-bottom: 24px;
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  color: #242A33;
}

.verification__benefits {
  margin-bottom: 24px;
  padding: 24px;
  background: #FFFFFF;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
}

.verification__benefits-title {
  margin-top: 0;
  margin-bottom: 8px;
  font-weight: 600;
  font-size: 18px;
  line-height: 26px;
  color: #242A33;
}

.verification__benefits-list {
  margin: 0;
  margin-bottom: 24px;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.verification__benefit {
  list-style: none;
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 8px;
  align-items: center;
}

.verification__benefit-tick {
  width: 16px;
  height: 16px;
  background-image: url(../../images/green-tick.svg);
  background-position: center;
  background-repeat: no-repeat;
}

.verification__benefit-text {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #636870;
  display: grid;
  gap: 8px;
  align-items: center;
  grid-template-columns: auto 1fr;
}

.verification__benefit-icon {
  width: 14px;
  height: 14px;
  background-image: url(../../images/verification-tick.svg);
  background-position: center;
  background-repeat: no-repeat;
}

.verification__container {
  display: flex;
  justify-content: space-between;
}

.verification__price {
  margin: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #FF4F9F;
}

.verification__rate {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
}

.verification__form {
  padding: 24px;
  background: #FFFFFF;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-areas: 
  'title-photo-main title-photo-passport'
  'photo-main-page photo-passport-with-person'
  'button button';
  gap: 0 48px;
}

.verification__form-title {
  margin-top: 0;
  margin-bottom: 16px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #303030;
}

.verification__form-title_main {
  grid-area: title-photo-main;
}

.verification__form-title_passport-with-person {
  grid-area: title-photo-passport;
}
.verification__form-label {
  position: relative;
}

.verification__form-label_passport-photo {
  grid-area: photo-main-page;
}

.verification__form-input_invisible {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}

.verification__form-input {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 8px;
  background: #FAFBFD;
  border: 1px dashed #A6ADB8;
  border-radius: 12px;
  height: 253px;
  cursor: pointer;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}

.verification__form-button_delete {
  padding: 0;
  position: absolute;
  width: 32px;
  height: 32px;
  border: none;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 8px;
  cursor: pointer;
  background-image: url(../../images//delete-photo-button.svg);
  background-repeat: no-repeat;
  background-position: center;
  right: 8px;
  bottom: 8px;
}

.verification__form-label_passport-with-person-photo {
  grid-area: photo-passport-with-person;
}

.verification__form-icon {
  width: 24px;
  height: 24px;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url(../../images/photo-icon.svg);
}

.verification__form-subtitle {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #636870;
}

.verification__form-button {
  margin-top: 24px;
  padding: 14px 41.5px;
  border: none;
  background-color: #6C26FF;
  box-shadow: 0px 15px 30px rgba(108, 38, 255, 0.15);
  border-radius: 16px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #FFFFFF;
  cursor: pointer;
  grid-area: button;
  justify-self: baseline;
}

@media screen and (max-width: 992px) {
  .verification__form {
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  .verification__form-title {
    margin: 0;
  }

  .verification__form-button {
    margin-top: 8px;
  }
}

@media screen and (max-width: 576px) {
  .verification__title {
    margin-bottom: 39px;
    font-size: 18px;
    line-height: 26px;
  }

  .verification__benefits {
    padding: 16px;
  }

  .verification__benefits-list {
    margin-bottom: 16px;
  }

  .verification__form {
    padding: 16px;
  }

  .verification__form-input {
    height: 160px;
  }

  .verification__form-title {
    font-size: 14px;
    line-height: 20px;
  }

  .verification__form-button {
    padding: 12px auto;
    font-size: 14px;
    line-height: 20px;
  }
}