.actor {
  padding: 24px;
  background: #FFFFFF;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
  display: grid;
  grid-template-columns: auto auto 1fr;
  grid-template-rows: repeat(3, auto);
  gap: 0 24px;
  grid-template-areas:
  'video info rating'
  'video name rating'
  'video tags tags';
  position: relative;
}

.actor_postion_promo {
  grid-template-columns: repeat(2, auto);
  grid-template-rows: auto;
  grid-template-areas:
  'video video'
  'info rating'
  'name name'
  'tags tags';
  gap: 0;
}

.actor__video {
  grid-area: video;;
}

.actor__video_postion_promo {
  margin-bottom: 24px;
}

.actor__play-video {
  width: 15px;
  height: 15px;
  border: none;
  background: none;
  background-image: url(../../images/Subtract.svg);
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  bottom: 32px;
  left: 32px;
}

.actor__play-video_postion_promo {
  top: 36px;
  left: 36px;
}

.actor__info {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
  grid-area: info;
}

.actor__name {
  margin-top: 0;
  margin-bottom: 12px;
  font-weight: 600;
  font-size: 18px;
  line-height: 26px;
  color: #242A33;
  display: flex;
  align-items: center;
  gap: 8px;
  grid-area: name;
}

.actor__tick {
  width: 14px;
  height: 14px;
  background-color: #1BEE6F;
  background-image: url(../../images/tick.svg);
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 50%;
}

.actor__tags {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 7px;
  grid-area: tags;
}

.actor__tag {
  padding: 2px 8px;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #636870;
  background-color: #F1F3F6;
  border-radius: 8px;
}

.acor__rating-container {
  display: flex;
  align-items: center;
  align-self: baseline;
  gap: 6px;
  grid-area: rating;
  justify-self: flex-end;
}

.acor__star {
  width: 20px;
  height: 20px;
  background-image: url(../../images/Star.svg);
}

.acor__rating {
  margin: 0;
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
  color: #242A33;
}

.actor__video_postion_promo {
  width: 100%;
}

@media screen and (max-width: 1400px) {
  .actor {
    padding: 12px;
    gap: 0 12px;
  }

  .actor_postion_promo {
    padding: 24px;
    gap: 0 24px;
  }
}

@media screen and (max-width: 1200px) {
  .actor_postion_promo {
    padding: 12px;
    gap: 0 12px;
  }
}

@media screen and (max-width: 768px) {
  .actor__video {
    width: 60px;
    height: 60px;
  }

  .actor__play-video {
    bottom: 15px;
    left: 15px;
  }

  .actor__info {
    font-size: 10px;
    line-height: 12px;
  }

  .actor__name {
    margin-bottom: 8px;
    font-size: 16px;
    line-height: 24px;
  }

  .actor__tags {
    gap: 4px;
  }

  .actor__tag {
    font-size: 10px;
    line-height: 12px;
  }

  .acor__star {
    width: 16px;
    height: 16px;
    background-size: cover;
  }

  .acor__rating {
    font-size: 10px;
    line-height: 12px;
  }
}