.questionary__text-areas {
  margin-top: 16px;
  margin-bottom: 36px;
  display: flex;
  justify-content: space-between;
  gap: 16px;
}

.questionary__container_attitude {
  display: grid;
  gap: 16px;
  grid-template-columns: repeat(3, 1fr);
}

.questionary__container_interests {
  display: grid;
  gap: 16px;
  grid-template-columns: repeat(4, 1fr);
}

.questionary__min-text {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #999999;
  position: absolute;
  bottom: -20px;
}

@media screen and (max-width: 992px) {
  .questionary__text-areas {
    flex-direction: column;
  }

  .questionary__container_attitude {
    display: flex;
  }
  
  .questionary__container_interests {
    display: flex;
  }
}