.edit-profile__button {
  margin-top: 24px;
  margin-bottom: 24px;
  border: 0;
  padding: 0;
  background-color: transparent;
  display: flex;
  gap: 8px;
  align-items: center;
  cursor: pointer;
}

.edit-profile__subtitle {
  margin: 0;
  font-weight: 600;
  font-size: 18px;
  line-height: 26px;
  color: #303030;
}

.edit-profile__button-arrow {
  width: 16px;
  height: 16px;
  background-repeat: no-repeat;
  transform: translateY(25%);
  background-image: url(../../../images/arrow-right.svg);
}

.edit-profile__button-arrow_filter-open {
  background-image: url(../../../images/arrow-bottom.svg);
}

.edit-profile__container {
  border-bottom: 1px solid #D9DCE1;
}

.edit-profile__choose-theme, .edit-profile__choose-type {
  margin-top: 0;
  margin-bottom: 24px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #303030;
}