.question-list {
  
}

.question-list__title {
  margin-top: 0;
  margin-bottom: 4px;
  font-weight: 600;
  font-size: 32px;
  line-height: 38px;
  color: #242A33;
}

.question-list__subtitle {
  margin-top: 0;
  margin-bottom: 24px;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
}

@media screen and (max-width: 768px) {
  .question-list__title {
    font-size: 16px;
    line-height: 24px;
  }

  .question-list__subtitle {
    font-size: 10px;
    line-height: 12px;
  }
}