.history__titles {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
}

.history__title {
  padding: 16px;
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #242A33;
}

.history__items {
  margin: 0;
  padding: 0;
}

@media screen and (max-width: 768px) {
  .history {
    padding: 0;
    background: none;
    box-shadow: none;
  }

  .history__titles {
    display: none;
  }
}