.popup-casting-confirm {
  visibility: hidden;
  display: flex;
  opacity: 0;
  transition: .5s;
  position: fixed;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0,0,0,0.5);
}

.popup-casting-confirm_opened {
  visibility: visible;
  opacity: 1;
  transition: .5s;
}

.popup-casting-confirm__container {
  min-width: 397px;
  background-color: #FFFFFF;
  padding: 99px 24px 55px;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
  position: relative;
  display: flex;
  flex-direction: column;
}

.popup-casting-confirm__icon {
  width: 40px;
  height: 40px;
  background-image: url(../../../images/info-icon-orange.svg);
  background-position: center;
  background-repeat: no-repeat;
  align-self: center;
}

.popup-casting-confirm__title {
  margin-top: 36px;
  margin-bottom: 16px;
  max-width: 400px;
  text-align: center;
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  color: #242A33;
}

.popup-casting-confirm__button_close {
  width: 14px;
  height: 14px;
  padding: 0;
  background: none;
  border: none;
  background-image: url(../../../images/button-close-popup.svg);
  position: absolute;
  top: 24px;
  right: 24px;
  cursor: pointer;
}

.popup-casting-confirm__text {
  margin-top: 0;
  margin-bottom: 32px;
  text-align: center;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #636870;
}

.popup-casting-confirm__buttons {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 32px;
}

.popup-casting-confirm__button {
  padding: 14px 0;
  border: none;
  cursor: pointer;
  border-radius: 16px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
}

.popup-casting-confirm__button_confirm {
  border: 1px solid #E2E5EA;
  background: #FFFFFF;
  color: #303030;
}

.popup-casting-confirm__button_refuse {
  background: #6C26FF;
  box-shadow: 0px 15px 30px rgba(108, 38, 255, 0.15);
  color: #FFFFFF;
}

@media screen and (max-width: 992px) {
  .popup-casting-confirm__container {
    width: 100vw;
    height: 100vh;
    justify-content: center;
    padding: 0;
    border-radius: 0;
  }

  .popup-casting-confirm__title {
    max-width: none;
  }

  .popup-casting-confirm__buttons {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    gap: 16px;
  }

  .popup-casting-confirm__button {
    min-width: 312px;
  }
}

@media screen and (max-width: 768px) {
  .popup-casting-confirm__title {
    max-width: 380px;
    align-self: center;
  }
}

@media screen and (max-width: 576px) {
  .popup-casting-confirm__container {
    min-width: 0;
    height: calc(100vh - 72px);
    padding: 0 24px 72px 24px;
    display: grid;
    grid-template-rows: 1fr auto auto 1fr;
    justify-items: center;
  }

  .popup-casting-confirm__icon {
    align-self: flex-end;
  }

  .popup-casting-confirm__title {
    font-size: 18px;
    line-height: 26px;
  }

  .popup-casting-confirm__button {
    width: 100%;
    font-size: 14px;
    line-height: 20px;
    align-self: flex-end;
  }
}