.auth__title_register {
  margin-bottom: 64px;
}

.auth__label {
  position: relative;
}

.auth__input_register {
  padding: 14px 45px 14px 16px;
  width: calc(100% - 61px);
}

.auth__input-icon {
  border: none;
  background-color: transparent;
  width: 22px;
  height: 22px;
  background-position: center;
  background-repeat: no-repeat;
  position: absolute;
  top: 15px;
  right: 15px;
  cursor: pointer;
}

.auth__input-icon:focus {
  outline: none;
}

.auth__input-icon_lock {
  background-image: url(../../../images/password-lock.svg);
}

.auth__input-icon_unlock {
  background-image: url(../../../images/password-unlock.svg);
}

@media screen and (max-width: 992px) {
  .auth__container_register {
    padding-top: 0;
    padding-bottom: 0;
  }
}