.custing-subscription {
  padding: 24px;
  background: #FFFFFF;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
  display: grid;
  grid-template-columns: 1fr auto;
  grid-template-areas: 
  'description description'
  'balance-info button';
  justify-content: space-between;
  align-items: center;
}

.custing-subscription__title {
  margin: 16px 0 24px;
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  color: #242A33;
}

.custing-subscription__description-block {
  padding-bottom: 24px;
  margin-bottom: 24px;
  border-bottom: 1px solid #F1F3F6;
  grid-area: description;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.custing-subscription__description {
  margin: 0;
  max-width: 760px;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #000000;
}

.custing-subscription__container {
  margin: 0;
  padding: 0;
  display: flex;
  gap: 24px;
}

.custing-subscription__container-item {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 6px;
  justify-content: space-between;
}

.custing-subscription__container-title {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
}

.custing-subscription__container-value {
  margin: 0;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: #242A33;
}

.custing-subscription__button {
  border: none;
  padding: 14px 82px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #FFFFFF;
  background: #6C26FF;
  box-shadow: 0px 15px 30px rgba(108, 38, 255, 0.15);
  border-radius: 16px;
  cursor: pointer;
  grid-area: button;
}

@media screen and (max-width: 768px) {
  .custing-subscription__container {
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
  }
}

@media screen and (max-width: 576px) {
  .custing-subscription {
    padding: 16px;
    grid-template-columns: 1fr;
    grid-template-areas: 
    'description'
    'balance-info'
    'button';
  }

  .custing-subscription__title {
    font-size: 18px;
    line-height: 26px;
  }

  .custing-subscription__button {
    margin-top: 24px;
  }
}