.notices {
  padding: 24px;
  max-width: 1396px;
  visibility: hidden;
  display: flex;
  opacity: 0;
  transition: .5s;
  position: fixed;
  justify-content: flex-end;
  width: calc(100% - 48px);
  height: calc(100% - 48px);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}

.notices_opened {
  visibility: visible;
  opacity: 1;
  transition: .5s;
  z-index: 1;
}

.notices__container {
  min-width: 490px;
  height: fit-content;
  background-color: #FFFFFF;
  padding: 24px 24px 12px;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.12), 10px 15px 40px rgba(26, 31, 39, 0.08);;
  border-radius: 16px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.notices__title {
  margin-top: 0;
  margin-bottom: 24px;
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  color: #242A33;
  align-self: flex-start;
}

.notices__button-close {
  width: 14px;
  height: 14px;
  padding: 0;
  background: none;
  border: none;
  background-image: url(../../../images/button-close-popup.svg);
  position: absolute;
  top: 31px;
  right: 24px;
  cursor: pointer;
}

.notices__list {
  margin: 0;
  padding: 16px 0 0 0;
  border-top: 1px solid #F1F3F6;
  display: flex;
  flex-direction: column;
  gap: 8px;
  overflow: auto;
}

.notices__list::-webkit-scrollbar {
  width: 0;
}

@media screen and (max-width: 1700px) {
  .notices {
    width: calc(100vw - 200px);
    padding: 24px 100px;
  }
}

@media screen and (max-width: 1400px) {
  .notices {
    width: calc(100vw - 120px);
    padding: 24px 60px;
  }
}

@media screen and (max-width: 768px) {
  .notices {
    padding: 0;
    width: 100%;
    height: 100%;
  }
  .notices__container {
    padding: 16px;
    min-width: 0;
    width: 100vw;
    height: 100vh;
    border-radius: 0;
  }
}