.my-castings__item {
  padding: 24px;
  text-decoration: none;
  background: #FFFFFF;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
  cursor: pointer;
  display: grid;
  grid-template-rows: 1fr auto;
  align-content: space-between;
}

.my-castings__item-container {
  margin-top: 16px;
  padding-top: 16px;
  border-top: 1px dashed #D9DCE1;
  display: grid;
  grid-template-columns: auto auto 1fr;
  gap: 8px;
  align-items: center;
}

.my-castings__item-status {
  margin: 0;
  padding: 2px 6px;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  border-radius: 4px;
}

.my-castings__item-status_active {
  color: #FFFFFF;
  background: #1BEE6F;;
}

.my-castings__item-status_in-process {
  color: #FFFFFF;
  background: #FFC224;
}

.my-castings__item-status_invited {
  color: #FFFFFF;
  background: #FF4F9F;
}

.my-castings__item-status_actor-choice {
  color: #242A33;
  background: #D9DCE1;
}

.my-castings__item-status_passed {
  color: #FFFFFF;
  background: #D9DCE1;
}

.my-castings__item-span {
  display: none;
  padding: 0;
  border: none;
  background-color: transparent;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
  text-align: start;
}

.my-castings__item-span_visible {
  display: block;
}

.my-castings__item-notice {
  display: none;
  width: 24px;
  height: 24px;
  background-position: center;
  background-repeat: no-repeat;
  justify-self: flex-end;
  grid-column-start: 3;
  grid-column-end: 4;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.my-castings__item-notice_new {
  display: block;
  background-image: url(../../../images/new-notice-icon.svg);
}

.my-castings__item-notice_chat {
  display: block;
  background-image: url(../../../images/chat-icon.svg);
}

@media screen and (max-width: 1200px) {
  .my-castings__item-container {
    grid-template-columns: auto 1fr;
    grid-template-areas: 
    'status chat-button'
    'text chat-button';
    justify-items: start;
    min-height: 64px;
  }

  .my-castings__item-status {
    grid-area: status;
  }

  .my-castings__item-span {
    grid-area: text;
  }

  .my-castings__item-notice {
    grid-area: chat-button;
  }
}

@media screen and (max-width: 992px) {
  .my-castings__item-container {
    min-height: 0;
  }
}

@media screen and (max-width: 576px) {
  .my-castings__item {
    padding: 16px;
  }
}