.balance-container {
  margin: 0;
  padding: 0;
  display: flex;
  gap: 24px;
}

.balance-container__balance {
  list-style: none;
  display: grid;
  grid-template-areas: 
  'title title'
  'value button';
  gap: 6px 10px;
  align-items: end;
}

.profile-promotion__payment {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 6px;
  justify-content: space-between;
}

.balance-container__title {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
}

.balance-container__title_balance {
  grid-area: title;
}

.balance-container__value {
  margin: 0;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: #242A33;
}

.balance-container__value_balance {
  grid-area: value;
}

.balance-container__link {
  text-decoration: none;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #6C26FF;
  grid-area: button;
}

@media screen and (max-width: 768px) {
  .balance-container {
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
  }
}