.filter {
  padding: 24px;
  margin-bottom: 24px;
  background-color: #FFFFFF;
  grid-area: filter;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
}

.filter_invisible {
  display: none;
}

.filter__items {
  display: flex;
  justify-content: space-between;
  margin-bottom: 24px;
  gap: 24px;
}

.filter__items_with-border {
  margin-bottom: 0;
  padding-bottom: 24px;
  border-bottom: 1px solid #F1F3F6;
}

.filter__buttons {
  padding-top: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.filter__button_view-more {
  padding: 0;
  border: none;
  background-color: transparent;
  display: flex;
  align-items: flex-end;
  gap: 8px;
  cursor: pointer;
}

.filter__button-text {
  margin: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #6C26FF;
}

.filter__button-arrow {
  width: 16px;
  height: 16px;
  background-repeat: no-repeat;
  transform: translateY(25%);
  background-image: url(../../../images/arrow-bottom.svg);
}

.filter__button-arrow_filter-open {
  background-image: url(../../../images/arrow-up.svg);
}

.filter__buttons-container {
  display: flex;
  gap: 8px;
}

.filter__button_reset {
  padding: 8px 16px;
  background-color: transparent;
  border: 1px solid #E2E5EA;
  border-radius: 16px;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #303030;
  cursor: pointer;
}

.filter__button_submitt {
  padding: 8px 16px;
  background-color: #6C26FF;
  box-shadow: 0px 15px 30px rgba(108, 38, 255, 0.15);
  border-radius: 16px;
  border: none;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #FFFFFF;
  cursor: pointer;
}

.filter_advanced-invisible {
  display: none;
}

.filter__container {
  border-bottom: 1px solid #F1F3F6;
}

.filter__body-tipe {
  display: flex;
  flex-direction: column;
}

/* .filter-radio:first-child {
  margin-bottom: 32px;
} */

.filter__body-tipe_invisible {
  display: none;
}

.filter__container-button {
  padding: 0;
  margin: 16px 0 24px;
  border: none;
  background-color: transparent;
  display: flex;
  align-items: flex-end;
  gap: 12px;
  cursor: pointer;
}

.filter__container-title {
  margin: 0;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #303030;
}

.filter__relations {
  display: flex;
  flex-direction: column;
}

.filter__relations_invisible {
  display: none;
}

@media screen and (max-width: 1200px) {
  .filter__items {
    gap: 8px;
  }
}

@media screen and (max-width: 992px) {
  .filter__items {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 16px 16px;
  }
}

@media screen and (max-width: 768px) {
  .filter {
    padding: 16px 16px 32px;
    position: absolute;
    z-index: 1;
    width: calc(100% - 32px);
  }
  .filter__items {
    display: flex;
    flex-direction: column;
  }

  .filter__buttons {
    flex-direction: column;
    gap: 24px;
    align-items: stretch;
  }

  .filter__button_view-more {
    align-self: center;
  }

  .filter__buttons-container {
    flex-direction: column-reverse;
  }
}