
.priveleged-actors__list-item {
  padding: 24px;
  background-color: #FFFFFF;
  list-style: none;
  border-radius: 16px;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  display: flex;
  flex-direction: column;
  position: relative;
}

.priveleged-actors__icons {
  position: absolute;
  top: 36px;
  left: 36px;
  display: flex;
  gap: 10px;
}

.priveleged-actors__play-video {
  width: 15px;
  height: 15px;
  border: none;
  background: none;
  background-image: url(../../images/Subtract.svg);
}
.priveleged-actors__container {
  margin-top: 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}


.priveleged-actors__online {
  padding: 2px 8px 2px 2px;
  background: rgba(255, 79, 159, 0.1);
  border: none;
  border-radius: 8px;
  display: flex;
}

.priveleged-actors__online-icon {
  margin-right: 4px;
  width: 12px;
  height: 12px;
  background-image: url(../../images/Record.svg);
}

.priveleged-actors__online-text {
  margin: 0;
  font-weight: 400;
  font-size: 9px;
  line-height: 11px;
  color: #FF4F9F;
}

.priveleged-actors__online_position_absolute {
  background-color: #FF4F9F;
}

.priveleged-actors__online-icon_position_absolute {
  background-image: url(../../images/Record-white.svg);
}

.priveleged-actors__online-text_position_absolute {
  color: #FFFFFF;
}

@media screen and (max-width: 1400px) {
  .priveleged-actors__list-item {
    padding: 12px;
  }
}