.main-info {
  padding: 24px;
  margin-bottom: 24px;
  background: #FFFFFF;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
  display: flex;
  gap: 24px;
  position: relative;
}

.main-info__verification {
  padding: 8px 16px;
  position: absolute;
  right: 24px;
  top: 24px;
  display: flex;
  gap: 8px;
  background: #E7FFF0;
  border-radius: 8px;
}

.main-info__verification-text {
  margin: 0;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #242A33;
}

.main-info__verification-icon {
  background-image: url(../../../images/verification-tick.svg);
  width: 14px;
  height: 14px;
  background-position: center;
  background-repeat: no-repeat;
}

.main-info__not-verification {
  position: absolute;
  right: 24px;
  top: 24px;
  display: flex;
  gap: 8px;
}

.main-info__not-verification-icon {
  background-image: url(../../../images/verification-tick-grey.svg);
  width: 14px;
  height: 14px;
  background-position: center;
  background-repeat: no-repeat;
}

.main-info__not-verification-text {
  margin: 0;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #242A33;
}


.main-info__button-video {
  padding: 0;
  position: absolute;
  top: 36px;
  left: 36px;
  border: none;
  background-color: transparent;
  background-image: url(../../../images/Subtract.svg);
  width: 15px;
  height: 15px;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
}

.main-info__photos {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.main-info__video {
  width: 100%;
}

.main-info__tags {
  margin-bottom: 10px;
  display: flex;
  gap: 8px;
}

.main-info__name-container {
  margin-bottom: 8px;
  display: flex;
  align-items: center;
  gap: 12px;
}

.main-info__tag {
  margin: 0;
  padding: 2px 8px;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #636870;
  background: #F1F3F6;
  border-radius: 8px;
}

.main-info__name {
  margin: 0;
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  color: #303030;
}

.main-info__star-container {
  padding: 2px 4px;
  display: flex;
  align-items: center;
  gap: 6px;
  background: #FFF9EA;
  border-radius: 4px;
}

.main-info__star-icon {
  display: block;
  width: 20px;
  height: 20px;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(../../../images/Star.svg);
}

.main-info__star-value {
  margin: 0;
  font-weight: 600;
  font-size: 12px;
  line-height: 18px;
  color: #242A33;
}

.main-info__info-container {
  display: flex;
  gap: 8px;
  margin-bottom: 16px;
}

.main-info__info-container-title {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #636870;
  padding-right: 8px;
  border-right: 1px solid #D9DCE1;
}

.main-info__info-container-title:last-of-type {
  border-right: none
}

.main-info__contacts {
  margin-top: 0;
  margin-bottom: 16px;
  padding: 0;
  padding-top: 16px;
  border-top: 1px solid #F1F3F6;
  display: flex;
  gap: 24px;
}

.main-info__contact {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.main-info__contact-title {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
}

.main-info__contact-subtitle {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #242A33;
}

.main-info__social-networks {
  margin: 0;
  padding: 0;
  display: flex;
  gap: 8px;
}

.main-info__social-network {
  list-style: none;
  cursor: pointer;
}

.main-info__about-container {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.main-info__about-container-item {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.main-info__about-container-title {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
}

.main-info__about-container-subtitle {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #242A33;
}

@media screen and (max-width: 1100px) {
  .main-info__name {
    font-size: 20px;
  }

  .main-info__info-container-title {
    font-size: 12px;
  }

}

@media screen and (max-width: 992px) {
  .main-info {
    flex-direction: column;
    padding: 44px;
  }

  .main-info__verification {
    display: none;
  }
}

@media screen and (max-width: 576px) {
  .main-info {
    padding: 16px;
  }

  .main-info__name-container {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }

  .main-info__name {
    font-size: 18px;
    line-height: 26px;
  }

  .main-info__info-container {
    flex-direction: column;
    align-items: flex-start;
    gap: 2px;
  }

  .main-info__contacts {
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
  }

  .main-info__info-container-title {
    font-size: 12px;
    line-height: 18px;
    padding-right: 0;
    border-right: none;
  }

  .main-info__contact-subtitle {
    font-size: 14px;
    line-height: 20px;
  }

  .main-info__about-container {
    flex-direction: column;
    align-items: flex-start;
  }

  .main-info__about-container-subtitle {
    font-size: 14px;
    line-height: 20px;
  }
}