.header {
  background-color: #FFFFFF;
}

.header__wrapper {
  max-width: 1396px;
  margin: 0 auto;
  padding: 16px 131px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__container {
  display: flex;
  align-items: center;
  gap: 48px;
}

.header__links {
  display: flex;
  gap: 34px;
}

.header__link {
  text-decoration: none;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #636870;
}

.header__link_active {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #242A33;
}

.header__link_type_signin {
  background-color: #6C26FF;
  padding: 12px 20px;
  border-radius: 16px;
  box-shadow: 0px 15px 30px rgba(108, 38, 255, 0.15);
  display: flex;
  gap: 8px;
  align-items: center;
}

.header__link-text {
  margin: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #FFFFFF;
}

.header__link-img {
  width: 22px;
  height: 22px;
  background-image: url(../../images/SignIn-Icon.svg);
}

.heder__mobile-menu {
  width: 16px;
  height: 12px;
  border: none;
  background: transparent;
  background-image: url(../../images/Mobile-menu-button.svg);
  background-position: center;
  background-repeat: no-repeat;
}

.header__actor-profile-block {
  display: flex;
  gap: 24px;
}

.header__actor-profile {
  display: grid;
  justify-items: end;
  grid-template-columns: repeat(2, auto);
  grid-template-areas: 
  'name photo'
  'email photo';
  gap: 2px 8px;
  position: relative;
  cursor: pointer;
}

.header__actor-profile-name {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #242A33;
  grid-area: name;
}

.header__actor-profile-email {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
  grid-area: email;
}

.header__notice {
  position: absolute;
  border-radius: 50%;
  width: 10px;
  height: 10px;
  background: #FF4F9F;
  bottom: 0;
}

.header__notice_invisible {
  display: none;
}

.header__actor-profile-photo {
  grid-area: photo;
}

.header__bell {
  display: none;
}

.header__new-cast {
  padding: 12px 20px;
  text-decoration: none;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #FFFFFF;
  background: #6C26FF;
  box-shadow: 0px 15px 30px rgba(108, 38, 255, 0.15);
  border-radius: 16px;
  cursor: pointer;
}

@media screen and (max-width: 1700px) {
  .header__wrapper {
    padding: 16px 100px;
  }
}

@media screen and (max-width: 1400px) {
  .header__wrapper {
    padding: 16px 60px;
  }
}

@media screen and (max-width: 995px) {
  .header__new-cast {
    display: none;
  }
}

@media screen and (max-width: 768px) {
  .header__wrapper {
    padding: 18px 20px;
  }

  .header__link_type_signin {
    padding: 0;
    background-color: transparent;

  }

  .header__link-text {
    display: none;
  }

  .header__link-img {
    width: 18px;
    height: 20px;
    background-image: url(../../images/Signin-Icon-Mobile.svg);
  }

  .header__actor-profile-block {
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-template-areas: 
    'bell photo';
    align-items: center;
    gap: 27px;
    position: static;
  }

  .header__actor-profile {
    grid-area: photo;
    display: block;
  }

  .header__actor-profile-name {
    display: none;
  }

  .header__actor-profile-email {
    display: none;
  }

  .header__actor-profile-photo {
    width: 24px;
    height: 24px;
  }

  .header__notice {
    display: none;
  }

  .header__bell {
    padding: 0;
    border: none;
    background-color: transparent;
    background-position: center;
    background-repeat: no-repeat;
    display: block;
    width: 18px;
    height: 20px;
    background-image: url(../../images/bell-icon.svg);
    grid-area: bell;
  }
}