.popup-payment {
  visibility: hidden;
  display: flex;
  opacity: 0;
  transition: .5s;
  position: fixed;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0,0,0,0.5);
}

.popup-payment_opened {
  visibility: visible;
  opacity: 1;
  transition: .5s;
}

.popup-payment__container {
  min-width: 397px;
  background-color: #FFFFFF;
  padding: 99px 24px 55px;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.popup-payment__icon {
  width: 40px;
  height: 40px;
  background-image: url(../../../images/payment-success-icon.svg);
  background-position: center;
  background-repeat: no-repeat;
}

.popup-payment__title {
  margin-top: 36px;
  margin-bottom: 8px;
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  color: #242A33;
}

.popup-payment__button_close {
  width: 14px;
  height: 14px;
  padding: 0;
  background: none;
  border: none;
  background-image: url(../../../images/button-close-popup.svg);
  position: absolute;
  top: 24px;
  right: 24px;
  cursor: pointer;
}

.popup-payment__text {
  margin-top: 0;
  margin-bottom: 32px;
  max-width: 307px;
  text-align: center;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #636870;
}

.popup-payment__button {
  padding: 14px 83.5px;
  border: none;
  background: #6C26FF;
  box-shadow: 0px 15px 30px rgba(108, 38, 255, 0.15);
  border-radius: 16px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #FFFFFF;
  cursor: pointer;
}

@media screen and (max-width: 992px) {
  .popup-payment__container {
    width: 100vw;
    height: 100vh;
    justify-content: center;
    padding: 0;
    border-radius: 0;
  }
}

@media screen and (max-width: 576px) {
  .popup-payment__container {
    min-width: 0;
    height: calc(100vh - 72px);
    padding: 0 24px 72px 24px;
    display: grid;
    grid-template-rows: 1fr auto auto 1fr;
    justify-items: center;
  }

  .popup-payment__icon {
    align-self: flex-end;
  }

  .popup-payment__title {
    font-size: 18px;
    line-height: 26px;
  }

  .popup-payment__button {
    width: 100%;
    font-size: 14px;
    line-height: 20px;
    align-self: flex-end;
  }
}