.tabs__buttons {
  display: flex;

  border-bottom: 1px solid #D9DCE1;;
}

.tabs__button {
  width: 133px;
  background-color: transparent;
  border: none;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #242A33;
  cursor: pointer;
  padding-bottom: 10px;
}

.tabs__button_active {
  border-bottom: 2px solid #6C26FF;
}

@media screen and (max-width: 576px) {
  .tabs__button {
    font-size: 14px;
    line-height: 20px;
  }
}