.edit-profile__title {
  margin-top: 0;
  margin-bottom: 24px;
  font-weight: 600;
  font-size: 24px;
  line-height: 30px;
  color: #242A33;
}

.edit-profile__form {
  padding: 24px;
  background: #FFFFFF;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
  display: flex;
  flex-direction: column;
}

.edit-profile__save-button {
  padding: 14px 77.5px;
  border: none;
  background: #6C26FF;
  box-shadow: 0px 15px 30px rgba(108, 38, 255, 0.15);
  border-radius: 16px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #FFFFFF;
  align-self: flex-end;
}

@media screen and (max-width: 992px) {
  .edit-profile__save-button {
    align-self: auto;
    font-size: 14px;
    line-height: 20px;
  }
}

@media screen and (max-width: 762px) {
  .edit-profile__form {
    padding: 16px;
  }
}