.auth__form {
  min-width: 400px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.auth__title {
  margin-top: 0;
  margin-bottom: 48px;
  font-weight: 600;
  font-size: 32px;
  line-height: 38px;
  color: #242A33;
  text-align: center;
}

.auth__input {
  padding: 14px 16px;
  margin-bottom: 16px;
  border: 1px solid #E2E5EA;
  border-radius: 16px;
  background: #FFFFFF;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #303030;
}

.auth__input_login {
  padding: 14px 45px 14px 16px;
  width: calc(100% - 61px);
}

.auth__input::placeholder {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #999999;
}

.auth__button {
  margin-top: 16px;
  margin-bottom: 48px;
  padding: 17px;
  border: none;
  background: #6C26FF;
  box-shadow: 0px 15px 30px rgba(108, 38, 255, 0.15);
  border-radius: 16px;
  font-weight: 500;
  font-size: 18px;
  line-height: 26px;
  color: #FFFFFF;
  cursor: pointer;
}

.auth__link {
  text-decoration: none;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #6C26FF;
}

.auth__link:first-of-type {
  margin-bottom: 24px;
}

.auth__error {
  bottom: 0;
  position: absolute;
  font-size: 12px;
  color: red;
}

@media screen and (max-width: 992px) {
  .auth__title {
    display: none;
  }

  .auth__button {
    margin-top: 16px;
    margin-bottom: 24px;
  }
}

@media screen and (max-width: 576px) {
  .auth__form {
    min-width: auto;
    width: 100%;
  }
}