.reviews-casting {
  padding: 24px;
  background: #FFFFFF;
  box-shadow: 2px 5px 15px rgba(26, 31, 39, 0.02), 10px 15px 40px rgba(26, 31, 39, 0.03);
  border-radius: 16px;
}

.reviews-casting__subtitle {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
}

.reviews-title {
  margin-top: 8px;
  margin-bottom: 24px;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #242A33;
}

.revies-casting__review {
  padding: 0;
  margin-top: 16px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 24px;
}

@media screen and (max-width: 992px) {
  .revies-casting__review {
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 576px) {
  .reviews-casting {
    padding: 16px;
  }

  .revies-casting__review {
    gap: 16px;
  }

  .reviews-title {
    margin-bottom: 16px;
    font-size: 14px;
    line-height: 20px;
  }
}