.searh-form {
  justify-self: flex-end;
}

.searh-form__container {
  display: flex;
  align-items: center;
  gap: 8px;
  position: relative;
}

.searh-form__input {
  width: 400px;
  margin: 0;
  padding: 12px 16px;
  border: 1px solid #E2E5EA;
  border-radius: 16px;
  background-color: #FFFFFF;
}

.searh-form__input_display_none {
  display: none;
}

.searh-form__input::placeholder {
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #999999;
}

.searh-form__button_open-filter {
  margin: 0;
  padding: 0;
  width: 44px;
  height: 44px;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(../../images/filter-button.svg);
  border: 1px solid #636870;
  border-radius: 16px;
  background-color: #F1F3F6;
  cursor: pointer;
  position: relative;
}

.searh-form__button_set {
  background-image: url(../../images/filter-button-set.svg);
}

.searh-form__button_submit {
  margin: 0;
  padding: 0;
  width: 44px;
  height: 44px;
  background-repeat: no-repeat;
  background-color: transparent;
  background-position: center;
  border: 1px solid #6C26FF;
  border-radius: 16px;
  background-image: url(../../images/loupe.svg);
}

.searh-form__filter-set {
  position: absolute;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #FF4F9F;
  bottom: 9px;
  right: 9px;
}

@media screen and (max-width: 768px) {
  .searh-form__input {
    width: 100%;
  }

  .searh-form__buttons {
    grid-area: buttons;
  }

  .searh-form__buttons {
    display: flex;
    gap: 16px;
    position: absolute;
    top: -60px;
    right: 0;
  }
}