.promo__item {
  position: relative;
  width: 100%;
  cursor: pointer;
}

.promo__title {
  margin: 0;
  font-weight: 600;
  font-size: 18px;
  line-height: 26px;
  color: #FFFFFF;
  position: absolute;
  bottom: 24px;
  left: 24px;
}

.promo__text {
  margin: 0;
  padding: 2px 8px;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #FFFFFF;
  position: absolute;
  top: 5%;
  right: 5%;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 8px;
}

.promo__image {
  width: 100%;
}

@media screen and (max-width: 1110px) {
  .promo__title {
    bottom: 16px;
    left: 16px;
  }
}

@media screen and (max-width: 992px) {
  .promo__image {
    max-height: 200px;
  }
}
