.banner {
  padding: 24px;
  background-color: #242A33;
  border-radius: 16px;
  display: grid;
  justify-content: space-between;
  grid-template-columns: repeat(2, auto);
  grid-template-areas: 
  'title button'
  'subtitle button';
  grid-column-start: 1;
  grid-column-end: 3;
  cursor: pointer;
}

.banner__title {
  margin: 0;
  font-weight: 600;
  font-size: 18px;
  line-height: 26px;
  color: #FFFFFF;
  grid-area: title;
}

.banner__subtitle {
  margin: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #A6ADB8;
  grid-area: subtitle;
}

.banner__button {
  padding: 12px 31px;
  grid-area: button;
  background-color: #6C26FF;
  box-shadow: 0px 15px 30px rgba(108, 38, 255, 0.15);
  border: none;
  border-radius: 16px;
  cursor: pointer;
}

.banner__button-text {
  margin: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #FFFFFF;
}

@media screen and (max-width: 1110px) {
  .banner {
    grid-column-end: 2;
  }
}

@media screen and (max-width: 768px) {
  .banner {
    padding: 16px;
  }

  .banner__title {
    font-size: 16px;
    line-height: 24px;
  }

  .banner__button {
    padding: 0;
    width: 44px;
    height: 44px;
    border-radius: 16px;
    background-image: url(../../images/Bunner-button.svg);
    background-repeat: no-repeat;
    background-position: center;
  }

  .banner__button-text {
    display: none;
  }
}