.review {
  list-style: none;
  margin: 0;
  display: grid;
  grid-template-columns: auto auto 1fr;
  grid-template-rows: auto 1fr;
  grid-template-areas: 
  'photo name stars'
  'text text text';
  gap: 16px 8px;
}

.review__photo {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  grid-area: photo;
}

.review__name {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #242A33;
  grid-area: name;
}

.review__text {
  margin: 0;
  padding: 16px;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  color: #242A33;
  background: #F1F3F6;
  border-radius: 0px 8px 8px 8px;
  grid-area: text;
}

.review__stars {
  grid-area: stars;
  justify-self: flex-end;
}

@media screen and (max-width: 576px) {
  .review {
    grid-template-columns: auto 1fr;
    grid-template-rows: auto auto auto;
    grid-template-areas: 
    'photo name'
    'stars stars'
    'text text';
  }

  .review__stars {
    justify-self: flex-start;
  }
}