.priveleged-actors {
  grid-area: priveleged-actors;
}

.priveleged-actors_disable {
  display: none;
}

.priveleged-actors__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.priveleged-actors__question {
  margin-top: 20px;
  margin-bottom: 0;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #828D9E;
  text-align: center;
}