.rasting-reviews {
  grid-area: secondary-information;
}
.casting-reviews__title {
  margin-top: 0;
  margin-bottom: 24px;
  font-weight: 600;
  font-size: 18px;
  line-height: 26px;
  color: #303030;
}

.casting-reviews__list {
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 24px;
}

@media screen and (max-width: 992px) {
  .casting-reviews__list {
    grid-template-columns: 1fr;
    gap: 16px;
  }
}